<template>
  <sl-dialog
    no-header
    style="--header-spacing: 0; --body-spacing: 0; --footer-spacing: 0; --width: 42rem"
    @sl-after-hide="afterHide"
    @sl-after-show="afterShow"
    @sl-initial-focus="onFocus"
  >
    <div v-if="form" class="Modal">
      <div class="Modal__header">
        <h1 class="h1">
          {{ title }}
        </h1>
        <button class="Modal__close" name="button" type="button" @click="hide()">
          <sl-icon library="feather" name="x"></sl-icon>
        </button>
      </div>

      <div class="Modal__content">
        <!-- Description -->
        <div class="Form__group">
          <label class="Form__label" for="title">
            {{ $i18n('title') }}
          </label>
          <div>
            <sl-input
              id="title"
              :placeholder="$i18n('newtab_enter_name')"
              type="text"
              :value="form.title"
              @input="form.title = $event.target.value"
              @keydown.enter="submit"
            ></sl-input>
            <InputErrorMessage class="mt-2" :error="errors.title" />
          </div>
        </div>

        <!-- Sharing -->
        <div v-if="isPremium && !isLegacyPlan && sharing !== null" class="pt-6 mt-6 border-t">
          <sl-switch
            :checked="sharing && sharing.public == 1"
            @sl-change="setGroupSharing($event.target.checked ? 1 : 0)"
          >
            <div class="pl-2">
              <div class="font-bold">
                {{ $i18n('share_to_web') }}
              </div>
              <div class="text-sm text-gray-400">
                {{ $i18n('share_to_web_helper') }}
              </div>
            </div>
          </sl-switch>

          <div v-if="sharing.public == 1" class="mt-6">
            <div class="relative">
              <sl-input filled readonly :value="publicUrl"></sl-input>
              <div class="absolute top-0 right-0 mt-1.5 mr-2">
                <sl-button :disabled="isCopiedToClipboard" size="small" variant="default" @click="copyToClipboard">
                  {{ isCopiedToClipboard ? $i18n('copied') : $i18n('copy_link') }}
                </sl-button>
              </div>
            </div>
            <div class="flex justify-between items-center mt-4 space-x-8 text-sm">
              <div class="flex items-center space-x-2">
                <sl-icon library="feather" name="link" />
                <span>{{ $i18n('people_with_link_can') }}</span>
              </div>
              <div class="flex-1">
                <sl-select
                  class="w-full"
                  filled
                  hoist
                  size="small"
                  :value="sharing.editable"
                  @sl-change="setGroupEditable"
                >
                  <sl-option value="0">{{ $i18n('permissions_view_group') }}</sl-option>
                  <sl-option value="1">{{ $i18n('permissions_view_and_edit_group') }}</sl-option>
                </sl-select>
              </div>
            </div>
          </div>
        </div>

        <div v-else-if="!isPremium || isLegacyPlan" class="flex justify-between items-center pt-6 mt-6 border-t">
          <sl-switch disabled>
            <div class="pl-2">
              <div class="font-bold">
                {{ $i18n('share_to_web') }}
              </div>
              <div class="text-sm text-gray-400">
                {{ $i18n('share_to_web_helper') }}
              </div>
            </div>
          </sl-switch>
          <div
            class="p-1 px-2 ml-1 text-xs font-bold text-white bg-red-400 hover:bg-red-500 rounded cursor-pointer"
            @click="$emit('upgrade')"
          >
            Pro
          </div>
        </div>
      </div>

      <div class="Modal__footer">
        <div class="flex gap-2">
          <sl-button v-if="form.id !== undefined" @click="deleteGroup()">
            {{ $i18n('delete') }}
          </sl-button>
          <sl-button class="ml-auto" @click="hide()">
            {{ $i18n('cancel') }}
          </sl-button>
          <sl-button variant="primary" @click="submit()">
            {{ $i18n('save') }}
          </sl-button>
        </div>
      </div>
    </div>
  </sl-dialog>
</template>

<script>
import SyncService from '@/services/SyncService'
import ValidationService, { isRequired } from '@/services/ValidationService'

export default {
  props: {
    group: {
      type: Object,
      required: false,
      default: () => {}
    },
    isPremium: {
      type: Boolean,
      required: false,
      default: false
    },
    isLegacyPlan: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  emits: ['submit', 'upgrade', 'delete'],

  data() {
    return {
      form: {},
      errors: {},
      sharing: null,
      isLoadingSharing: false,
      isCopiedToClipboard: false
    }
  },

  computed: {
    title() {
      return this.form?.id !== undefined ? this.$i18n('newtab_edit_group') : this.$i18n('newtab_create_group')
    },

    publicUrl() {
      return `https://speeddial2.com/s/${this.sharing.key}`
    }
  },

  methods: {
    show(data) {
      this.reset()
      this.init()
      this.$el.show()
    },

    hide() {
      document.activeElement?.blur()
      this.$el.hide()
    },

    afterShow() {},

    afterHide() {},

    onFocus(e) {
      event.preventDefault()
      document.getElementById('title').focus({ preventScroll: true })
    },

    async init() {
      this.form = { ...this.group }
      if (!isNaN(this.group.id)) {
        this.isLoadingSharing = true
        this.sharing = await SyncService.getGroupSharing(this.group)
        this.isLoadingSharing = false
      }
    },

    reset() {
      this.form = {}
      this.errors = {}
      this.sharing = null
      this.isCopiedToClipboard = false
    },

    async validate() {
      this.errors = {}

      const rules = {
        title: [isRequired('Please enter title')]
      }

      const result = await ValidationService.validate(this.form, rules)

      if (!result.isValid) {
        this.result = undefined
        this.errors = result.errors
        return false
      }

      return true
    },

    async setGroupSharing(shared, editable) {
      this.sharing = await SyncService.setGroupSharing(this.group, shared, editable)
    },

    async setGroupEditable(e) {
      this.sharing = await SyncService.setGroupSharing(this.group, 1, e.target.value)
    },

    async copyToClipboard() {
      await navigator.clipboard.writeText(this.publicUrl)
      this.isCopiedToClipboard = true
    },

    deleteGroup() {
      this.$emit('delete', this.group)
      this.hide()
    },

    async submit() {
      if (!(await this.validate())) {
        return
      }

      this.$emit('submit', this.form)
      this.hide()
    }
  }
}
</script>
