<template>
  <div class="Bookmarks" :class="{ 'Bookmarks--isList': asList }">
    <BrowserBookmark
      v-for="(bookmark, index) in bookmarks"
      :key="bookmark.id"
      :as-list="asList"
      :bookmark="bookmark"
      :is-selected="allowKeyboard && index == selectedItem"
      @folderSelected="(id) => $emit('folderSelected', id)"
    />
  </div>
</template>

<script>
import BrowserBookmark from '@/components/BrowserBookmark.vue'
export default {
  components: {
    BrowserBookmark
  },
  props: {
    bookmarks: {
      type: Array,
      default: () => []
    },
    folder: {
      type: Object,
      default: () => null
    },
    asList: {
      type: Boolean,
      default: true
    },
    allowKeyboard: {
      type: Boolean,
      default: true
    }
  },

  emits: ['folderSelected', 'navigateToSearch', 'reset'],

  data() {
    return {
      selectedItem: -1
    }
  },
  created() {
    window.addEventListener('keydown', this.keydownHandler)
  },
  unmounted() {
    window.removeEventListener('keydown', this.keydownHandler)
  },
  methods: {
    scrollItemIntoView() {
      setTimeout(() => {
        const el = document.getElementsByClassName('Bookmark--isSelected')[0]
        if (el) {
          el.scrollIntoViewIfNeeded(false)
          el.focus()
        }
      })
    },
    toItem(index) {
      this.selectedItem = index
      this.scrollItemIntoView()
    },
    nextItem(offset = 1) {
      this.selectedItem = Math.min(this.bookmarks.length - 1, this.selectedItem + offset)
      this.scrollItemIntoView()
    },
    prevItem(offset = 1) {
      if (this.selectedItem == -1) {
        return
      }

      if (this.selectedItem === 0) {
        this.selectedItem = -1
        this.$emit('navigateToSearch')
        return
      }
      this.selectedItem = Math.max(0, this.selectedItem - offset)
      this.scrollItemIntoView()
    },
    keydownHandler(event) {
      if (!this.allowKeyboard) {
        return true
      }

      const isInput = event.target.tagName.includes('INPUT')
      const isMetaKey = event.metaKey || event.ctrlKey

      if (isInput && (event.keyCode === 37 || event.keyCode === 39)) {
        return true
      }

      const verticalOffset = this.selectedItem === -1 ? 1 : this.asList ? 1 : 5

      // Go back to parent with backspace
      if (!isInput && event.keyCode === 8) {
        event.preventDefault()

        if (this.folder && this.folder.parentId) {
          if (Number(this.folder.parentId) == 0) {
            this.$emit('folderSelected', null)
            this.$emit('reset')
          } else {
            this.$emit('folderSelected', this.folder.parentId)
          }
          this.selectedItem = 0
        } else if (this.selectedItem > 0) {
          this.selectedItem = 0
        } else {
          this.selectedItem = -1
          this.$emit('navigateToSearch')
        }
        return
      }

      if (isMetaKey && !isInput) {
        if (event.keyCode === 70) {
          this.$emit('navigateToSearch')
          return
        }

        if (event.keyCode === 38) {
          this.toItem(0)
          return
        }

        if (event.keyCode === 40) {
          this.toItem(this.bookmarks.length - 1)
          return
        }
      }

      if (event.keyCode === 40) {
        event.preventDefault()
        this.nextItem(verticalOffset)
      } else if (event.keyCode === 39) {
        // event.preventDefault()
        this.nextItem()
      } else if (event.keyCode === 38) {
        event.preventDefault()
        this.prevItem(verticalOffset)
      } else if (event.keyCode === 37) {
        // event.preventDefault()
        this.prevItem()
      } else if (event.keyCode === 13) {
        if (this.bookmarks[this.selectedItem] && !this.bookmarks[this.selectedItem].url) {
          this.$emit('folderSelected', this.bookmarks[this.selectedItem].id)
          this.selectedItem = 0
        }
      }
    }
  }
}
</script>

<style lang="postcss" scoped>
.Bookmarks {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  &--isList {
    flex-direction: column;
  }
}
</style>
