<template>
  <div class="flex overflow-hidden flex-col h-full">
    <div class="flex justify-between items-center px-12 pt-8 space-x-2 bg-white">
      <div class="flex items-center w-full">
        <div
          v-if="!isPremium"
          class="p-1 px-2 mr-2 text-xs font-bold text-white bg-red-400 hover:bg-red-500 rounded cursor-pointer"
          @click="$emit('upgrade')"
        >
          PRO
        </div>
        <div class="h3">Select font</div>
      </div>

      <sl-input
        inputmode="search"
        :placeholder="$i18n('search')"
        type="search"
        :value="fontQuery"
        @input="fontQuery = $event.target.value"
        @keydown.esc.stop="fontQquery = ''"
      >
        <sl-icon slot="prefix" library="feather" name="search"></sl-icon>
      </sl-input>

      <sl-button size="medium" @click="close()">
        <span>{{ $i18n('close') }}</span>
      </sl-button>
    </div>

    <div class="flex justify-between items-center py-4 px-12 capitalize border-b">
      <div v-for="category in categories" :key="category">
        <sl-checkbox :checked="category == selectedCategory" @sl-change="selectedCategory = category">
          {{ category }}
        </sl-checkbox>
      </div>
    </div>

    <div class="overflow-y-scroll py-8 px-12 h-full">
      <div class="grid grid-cols-2 gap-3">
        <div
          v-for="font in filteredFonts"
          :key="font.value"
          class="flex justify-center items-center p-8 py-4 h-16 truncate hover:bg-gray-50 rounded border cursor-pointer"
          :class="{ 'border-primary': selectedFont == font.value, 'cursor-not-allowed opacity-50': !isPremium }"
          @click="isPremium && setFont(font.value)"
        >
          <img
            v-if="font.image"
            loading="lazy"
            :src="`https://cdn.jsdelivr.net/gh/virae/google-fonts-preview-image@main/fonts/${font.image}.png`"
            style="max-height: 24px"
          />
          <div v-else>{{ font.label || font.value }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import uniq from 'lodash/uniq'
import { matchSorter } from 'match-sorter'

import { fonts } from '@/constants/fonts'
import { loadFont } from '@/utils/loadFont'

export default {
  name: 'FontPicker',

  props: {
    selectedFont: {
      type: String,
      default: ''
    },

    isPremium: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  data() {
    return {
      fonts,
      selectedCategory: 'All',
      fontQuery: ''
    }
  },

  computed: {
    categories() {
      return ['All', ...uniq(this.fonts.map(({ category }) => category))]
    },

    filteredFonts() {
      const query = this.fontQuery.trim()
      const fonts =
        this.selectedCategory !== 'All'
          ? this.fonts.filter(({ category }) => category === this.selectedCategory)
          : this.fonts

      if (query.length === 0) {
        return fonts
      }

      return matchSorter(fonts, query, { keys: ['value'] })
    }
  },

  methods: {
    setFont(font) {
      loadFont(font)
      this.$emit('change', font)
    },
    upgrade() {
      this.$emit('upgrade')
    },
    close() {
      this.$emit('close')
    }
  }
}
</script>
