<template>
  <sl-dialog
    no-header
    style="--header-spacing: 0; --body-spacing: 0; --footer-spacing: 0"
    @keyup.enter="submit"
    @keyup.esc="hide"
    @sl-request-close="onRequestClose"
  >
    <div class="Modal">
      <div class="Modal__header">
        <h1 class="h1">{{ title }}</h1>
        <button class="Modal__close" name="button" type="button" @click="cancel()">
          <sl-icon library="feather" name="x"></sl-icon>
        </button>
      </div>

      <div class="Modal__content">
        <p v-html="message"></p>
      </div>

      <div class="Modal__footer">
        <div class="flex justify-end space-x-2">
          <sl-button @click="cancel()">
            {{ $i18n('cancel') }}
          </sl-button>
          <sl-button variant="danger" @click="submit()">OK</sl-button>
        </div>
      </div>
    </div>
  </sl-dialog>
</template>

<script>
export default {
  props: {},

  data() {
    return {
      data: null,
      resolve: null,
      reject: null,
      title: '',
      message: ''
    }
  },

  methods: {
    show({ options, data } = {}) {
      return new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject
        this.data = data
        this.title = options?.title
        this.message = options?.message
        this.$el.show()
      })
    },

    onRequestClose(e) {
      e.preventDefault()
      this.cancel()
    },

    cancel() {
      this.resolve(false)
      this.$el.hide()
    },

    submit() {
      this.resolve(this.data || true)
      this.$el.hide()
    }
  }
}
</script>
