import throttle from 'lodash/throttle'

import defaultPreferences from '@/constants/defaultPreferences'
import themes from '@/constants/themes'
import StorageService from '@/services/StorageService'
import SyncService from '@/services/SyncService'

const syncPreferences = throttle((preferences) => {
  SyncService.updatePreferences(preferences)
}, 2500)

const types = {
  SET_PREFERENCES: 'SET_PREFERENCES',
  SET_HIDDEN_GROUPS: 'SET_HIDDEN_GROUPS'
}

const cachedPreferences = StorageService.load('preferences', defaultPreferences)
const cachedHiddenGroups = StorageService.load('hiddenGroups', [])
const installedOn = StorageService.load('installedOn')

if (installedOn) {
  browser.storage.local.set({ installedOn })
  StorageService.remove('installedOn')
}

const migrateTheme = (preferences) => {
  try {
    const copy = { ...preferences }

    const lightBg = preferences?.theme?.light?.backgroundImage?.indexOf('imgur') > -1
    const darkBg = preferences?.theme?.dark?.backgroundImage?.indexOf('imgurl') > -1

    if (lightBg) {
      const matchingLightTheme = themes.find(({ old_url }) => old_url === preferences.theme.light.backgroundImage)
      if (matchingLightTheme) {
        copy.theme.light.backgroundImage = matchingLightTheme.url
      }
    }

    if (darkBg) {
      const matchingDarkTheme = themes.find(({ old_url }) => old_url === preferences.theme.dark.backgroundImage)
      if (matchingDarkTheme) {
        copy.theme.dark.backgroundImage = matchingDarkTheme.url
      }
    }

    return copy
  } catch (e) {
    return preferences
  }
}

const getInitialState = async () => {
  // -----------------------------------
  // Start: migration to browser.storage
  // -----------------------------------

  const existingPreferences = await browser.storage.local.get({
    preferences: null,
    hiddenGroups: null
  })

  if (!existingPreferences.preferences) {
    await browser.storage.local.set({ preferences: cachedPreferences })
  }

  if (!existingPreferences.hiddenGroups) {
    await browser.storage.local.set({ hiddenGroups: cachedHiddenGroups })
  }

  // -----------------------------------
  // End: migration to browser.storage
  // -----------------------------------

  const result = await browser.storage.local.get({
    preferences: defaultPreferences,
    hiddenGroups: []
  })

  StorageService.persist('preferences', result.preferences)
  StorageService.persist('hiddenGroups', result.hiddenGroups)

  const { preferences, hiddenGroups } = result

  return {
    preferences: {
      ...defaultPreferences,
      ...preferences,

      bookmarks: {
        ...defaultPreferences.bookmarks,
        ...preferences.bookmarks
      },

      theme: {
        ...defaultPreferences.theme,
        ...preferences.theme,

        light: {
          ...defaultPreferences.theme.light,
          ...preferences.theme.light
        },
        dark: {
          ...defaultPreferences.theme.dark,
          ...preferences.theme.dark
        }
      }
    },

    hiddenGroups
  }
}

const state = {
  preferences: cachedPreferences,
  hiddenGroups: cachedHiddenGroups
}

const actions = {
  async init({ commit, dispatch, state }) {
    const { preferences, hiddenGroups } = await getInitialState()
    const migrated = migrateTheme(preferences)
    commit(types.SET_PREFERENCES, migrated)
    commit(types.SET_HIDDEN_GROUPS, hiddenGroups)
  },

  async reloadPreferences({ dispatch }) {
    const { preferences } = await getInitialState()
    const migrated = migrateTheme(preferences)
    dispatch('updatePreferences', { preferences: migrated, sync: false })
  },

  async updatePreferences({ commit, dispatch, state }, { preferences, sync = true }) {
    const migrated = migrateTheme(preferences)
    commit(types.SET_PREFERENCES, migrated)
    await browser.storage.local.set({ preferences: migrated })
    StorageService.persist('preferences', migrated)
    if (sync) {
      syncPreferences(migrated)
    }
  },

  updateAttribute({ dispatch, state }, { attribute, value }) {
    const preferences = {
      ...state.preferences,
      [attribute]: value
    }

    dispatch('updatePreferences', { preferences, sync: true })
  },

  updateDefaultGroupName({ dispatch }, name) {
    dispatch('updateAttribute', { attribute: 'defaultGroupName', value: name })
  },

  async updateHiddenGroups({ commit, dispatch, state }, hiddenGroups) {
    commit(types.SET_HIDDEN_GROUPS, hiddenGroups)
    await browser.storage.local.set({ hiddenGroups })
    StorageService.persist('hiddenGroups', hiddenGroups)
  }
}

const mutations = {
  [types.SET_PREFERENCES](state, preferences) {
    state.preferences = preferences
  },
  [types.SET_HIDDEN_GROUPS](state, hiddenGroups) {
    state.hiddenGroups = hiddenGroups
  }
}

const getters = {
  preferences(state) {
    return state.preferences
  },
  hiddenGroups(state) {
    return state.hiddenGroups
  },
  username(state) {
    return state.preferences?.username
  },
  password(state) {
    return state.preferences?.password
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
