<template>
  <ResponsiveAspectRatio :ratio="aspectRatio">
    <div
      class="flex flex-col justify-center items-center w-full h-full cursor-pointer"
      :class="{ 'AddButtonWrapper': !large }"
    >
      <div class="AddButton" :class="{ 'AddButton--large': large }" @click="$emit('onCreateBookmark')">
        <sl-icon class="title-1" name="plus"></sl-icon>
      </div>
      <div v-if="label" class="mt-4 opacity-50">
        {{ label }}
      </div>
    </div>
  </ResponsiveAspectRatio>
</template>

<script>
import ResponsiveAspectRatio from '@/components/ResponsiveAspectRatio'

export default {
  components: {
    ResponsiveAspectRatio
  },

  props: {
    large: {
      type: Boolean,
      default: false
    },

    label: {
      type: String,
      required: false
    }
  },

  data() {
    return {
      aspectRatio: Math.max(screen.height / screen.width, 9 / 16)
    }
  }
}
</script>

