import StorageService from '@/services/StorageService'
import { loadFont } from '@/utils/loadFont'

const defaultFontFamily = "system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif"
const BOOKMARK_MAX_WIDTH = 480

const types = {
  SET_DARK_OS_THEME: 'SET_DARK_OS_THEME',
  SET_RECENT_UPLOADS: 'SET_RECENT_UPLOADS'
}

const recentUploads = StorageService.load('recentUploads', [])

const getInitialState = () => ({
  darkOSTheme: window?.matchMedia('(prefers-color-scheme: dark)').matches,
  recentUploads
})

const state = getInitialState()

const actions = {
  initializeTheme({ commit, dispatch, state, rootState }, value) {
    if (rootState.preferences.preferences.theme.font) {
      loadFont(rootState.preferences.preferences.theme.font)
    }
  },
  setDarkOSTheme({ commit, dispatch, state }, value) {
    commit(types.SET_DARK_OS_THEME, value)
  },
  storeRecentUpload({ commit, dispatch, state }, url) {
    const recentUploads = [url, ...state.recentUploads].slice(0, 24)

    commit(types.SET_RECENT_UPLOADS, recentUploads)
    StorageService.persist('recentUploads', recentUploads)
  },
  removeRecentUpload({ commit, dispatch, state }, url) {
    const recentUploads = state.recentUploads.filter((u) => u !== url)

    commit(types.SET_RECENT_UPLOADS, recentUploads)
    StorageService.persist('recentUploads', recentUploads)
  }
}

const mutations = {
  [types.SET_DARK_OS_THEME](state, value) {
    state.darkOSTheme = value
  },
  [types.SET_RECENT_UPLOADS](state, recentUploads) {
    state.recentUploads = recentUploads
  }
}

const getters = {
  effectiveTheme(state, getters, rootState) {
    if (rootState.preferences.preferences.theme.theme === 'dark') {
      return 'dark'
    } else if (rootState.preferences.preferences.theme.theme === 'auto' && state.darkOSTheme) {
      return 'dark'
    } else {
      return 'light'
    }
  },

  theme(state, getters, rootState) {
    return rootState.preferences.preferences.theme[getters.effectiveTheme]
  },

  pageWrapperBackground(state, getters) {
    return {
      opacity: `${getters.theme.backgroundOpacity || 100}%`,
      backgroundImage: getters.theme.backgroundImage ? `url(${getters.theme.backgroundImage})` : '',
      backgroundSize: getters.theme.backgroundSize,
      backgroundRepeat: getters.theme.backgroundRepeat,
      backgroundPosition: (getters.theme.backgroundPosition || '').replace('-', ' ')
    }
  },

  pageWrapperBackgroundColor(state, getters) {
    return {
      backgroundColor: getters.theme.backgroundColor
    }
  },

  containerStyle(state, getters, rootState) {
    return {
      width: `${rootState.preferences.preferences.maxWidth}px`,
      maxWidth: `${rootState.preferences.preferences.columns * BOOKMARK_MAX_WIDTH}px`
    }
  },

  bookmarksStyle(state, getters, rootState) {
    return {
      ...getters['containerStyle'],
      'grid-template-columns': `repeat(${rootState.preferences.preferences.columns}, minmax(0, 1fr))`,
      'gap': `${rootState.preferences.preferences.spacing}px`
    }
  },

  bookmarkWrapperStyle(state, getters, rootState) {
    let borderRadius = '0'
    let padding = '0'

    switch (rootState.preferences.preferences.bookmarks.borderRadius) {
      case 'small':
        borderRadius = '0.25rem'
        break
      case 'medium':
        borderRadius = '0.375rem'
        break
      case 'large':
        borderRadius = '0.5rem'
        break
    }

    switch (rootState.preferences.preferences.bookmarks.padding) {
      case 'small':
        padding = '0.25rem'
        break
      case 'medium':
        padding = '0.375rem'
        break
      case 'large':
        padding = '0.5rem'
        break
    }

    return {
      backgroundColor: getters.theme.bookmarksBackgroundColor,
      borderColor: getters.theme.bookmarksBorderColor,
      color: getters.theme.bookmarksTextColor,
      maxWidth: `${BOOKMARK_MAX_WIDTH}px`,
      borderRadius,
      padding
    }
  },

  bookmarkTitleStyle(state, getters, rootState) {
    let padding = '0.5rem'
    let paddingLeft = '0.5rem'
    let paddingRight = '0.5rem'
    let marginBottom = '0'

    switch (rootState.preferences.preferences.bookmarks.padding) {
      case 'small':
        paddingLeft = '0.25rem'
        paddingRight = '0.25rem'
        marginBottom = '-0.25rem'
        break
      case 'medium':
        paddingLeft = '0.125rem'
        paddingRight = '0.125rem'
        marginBottom = '-0.375rem'
        break
      case 'large':
        paddingLeft = '0'
        paddingRight = '0'
        marginBottom = '-0.5rem'
        break
      default:
        paddingLeft = '0.5rem'
        paddingRight = '0.5rem'
    }

    return {
      fontFamily: `"${rootState.preferences.preferences.theme.font}", ${defaultFontFamily}`,
      fontSize: rootState.preferences.preferences.theme.fontSize
        ? `${rootState.preferences.preferences.theme.fontSize}%`
        : '100%',
      textAlign: rootState.preferences.preferences.bookmarks.alignTitle,
      padding,
      paddingLeft,
      paddingRight,
      marginBottom
    }
  },

  bookmarkStyle(state, getters, rootState) {
    return {
      backgroundColor: getters.theme.bookmarksInnerBackgroundColor
    }
  },

  textStyle(state, getters, rootState) {
    return {
      color: getters.theme.uiTextColor
    }
  },

  headerStyle(state, getters, rootState) {
    return {
      fontFamily: `"${rootState.preferences.preferences.theme.font}", ${defaultFontFamily}`,
      fontSize: rootState.preferences.preferences.theme.fontSize
        ? `${rootState.preferences.preferences.theme.fontSize}%`
        : '100%',
      backgroundColor: getters.theme.uiBackgroundColor,
      color: getters.theme.uiTextColor
    }
  },

  compactHeaderStyle(state, getters, rootState) {
    return {
      '--background-color': getters.theme.uiBackgroundColor,
      '--font-family': `"${rootState.preferences.preferences.theme.font}", ${defaultFontFamily}`,
      '--text-color': getters.theme.uiTextColor
    }
  },

  sidebarStyle(state, getters, rootState) {
    return {
      fontFamily: `"${rootState.preferences.preferences.theme.font}", ${defaultFontFamily}`,
      fontSize: rootState.preferences.preferences.theme.fontSize
        ? `${rootState.preferences.preferences.theme.fontSize}%`
        : '100%'
    }
  },

  activeItemStyle(state, getters, rootState) {
    return {
      borderColor: getters.theme.uiTextColor
    }
  },

  iconStyle(state, getters, rootState) {
    return {
      color: getters.theme.uiTextColor
    }
  },

  recentUploads(state) {
    return state.recentUploads
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
