<template>
  <a
    v-observe-visibility="{ callback: visibilityChanged, once: true }"
    class="Bookmark"
    :class="{ 'Bookmark--isSelected': isSelected }"
    :href="bookmark.url || '#'"
    @click.stop="!bookmark.url && $emit('folderSelected', bookmark.id)"
  >
    <div class="Bookmark__image">
      <img v-if="isVisible && bookmark.url" class="mx-auto w-4 h-4 rounded-full" :src="logo" />
      <svg
        v-else-if="isVisible"
        class="mx-auto w-5 h-5"
        enable-background="new 0 0 48 48"
        version="1"
        viewBox="0 0 48 48"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path d="M40,12H22l-4-4H8c-2.2,0-4,1.8-4,4v8h40v-4C44,13.8,42.2,12,40,12z" fill="#FFA000" />
        <path
          d="M40,12H8c-2.2,0-4,1.8-4,4v20c0,2.2,1.8,4,4,4h32c2.2,0,4-1.8,4-4V16C44,13.8,42.2,12,40,12z"
          fill="#FFCA28"
        />
      </svg>
    </div>
    <div class="Bookmark__title">
      <div class="Bookmark__name">
        <div class="truncate">{{ bookmark.title || hostname }}</div>
        <div v-if="bookmark.children" class="Bookmark__count">{{ bookmark.children.length }}</div>
      </div>
      <div v-if="bookmark.url" class="Bookmark__url">
        {{ hostname }}
      </div>
    </div>
  </a>
</template>

<script>
import { isFirefox } from '@/utils/browser'
import { getFaviconURL } from '@/utils/getFaviconURL'

export default {
  props: {
    bookmark: {
      type: Object
    },
    asList: {
      type: Boolean,
      default: true
    },
    isSelected: {
      type: Boolean,
      default: false
    }
  },
  emits: ['folderSelected'],
  data() {
    return {
      isVisible: false
    }
  },
  computed: {
    logo() {
      if (isFirefox()) {
        return `https://favicone.com/${this.hostname}?s=32`
      } else {
        return getFaviconURL(this.bookmark.url)
      }
    },
    hostname() {
      const hostname = (this.bookmark.url || '').split(/\/+/g)[1]
      return (hostname || '').replace('www.', '')
    }
  },
  methods: {
    visibilityChanged(isVisible) {
      this.isVisible = isVisible
    }
  }
}
</script>

<style lang="postcss" scoped>
.Bookmark {
  @apply flex text-sm items-center cursor-pointer space-x-2 p-3 py-1.5 hover:bg-gray-50 w-full truncate overflow-hidden ring-0 outline-none;

  &__image {
    @apply flex-shrink-0 w-5 h-5 flex items-center;
  }

  &__title {
    @apply w-full overflow-hidden;
  }
  &__name {
    @apply truncate flex items-center justify-between;
  }
  &__count {
    @apply opacity-50 text-xs;
  }

  &__url {
    @apply truncate text-gray-400;
    font-size: 0.7rem;
    line-height: 0.7rem;
  }

  &--isSelected {
    background: rgba(32, 33, 36, 0.08) !important;
  }
}
</style>
