<template>
  <div class="w-full">
    <div v-if="hasGroups" class="Header" :style="headerStyle">
      <draggable
        ref="groups"
        v-model="visibleGroups"
        class="Groups"
        v-bind="dragOptions"
        :class="{ 'Groups--dragging': drag }"
        :disabled="!canReorderGroups"
        item-key="id"
        @change="handleReorder"
        @end="drag = false"
        @start="drag = true"
      >
        <template #item="{ element }">
          <router-link
            class="Groups__item"
            :class="{ 'Groups__item--active': element.id == selectedGroup }"
            :data-id="element.id"
            :style="element.id == selectedGroup ? activeItemStyle : {}"
            :to="{ name: 'home', params: { id: element.id } }"
            @contextmenu.prevent="handleContextMenu($event, element)"
            @dragenter="allowDrop"
            @dragleave="clearDrop"
            @drop="handleDrop"
          >
            {{ element.title }}
          </router-link>
        </template>
        <template #footer>
          <div class="self-center -ml-3 opacity-0 hover:opacity-100 HeaderButton" @click="$emit('onCreateGroup')">
            <sl-icon
              class="flex shrink-0 items-center p-2 text-lg cursor-pointer"
              library="feather"
              name="plus"
            ></sl-icon>
          </div>
        </template>
      </draggable>
      <div class="HiddenGroups">
        <div
          class="HiddenGroups__toggle"
          :class="{ 'HiddenGroups__toggle--visible': hasHiddenGroups }"
          @click="dropdownOpen = !dropdownOpen"
        >
          <sl-icon library="feather" name="chevron-down"></sl-icon>
        </div>
        <transition name="dropdown">
          <div
            v-if="dropdownOpen && hasHiddenGroups"
            class="HiddenGroups__dropdown"
            :style="headerStyle"
            @click="dropdownOpen = false"
          >
            <draggable
              v-model="hiddenGroups"
              v-bind="dragOptions"
              :class="{ 'Groups--gragging': drag }"
              :disabled="!canReorderGroups"
              item-key="id"
              @change="handleReorder"
              @end="drag = false"
              @start="drag = true"
            >
              <template #item="{ element }">
                <router-link
                  class="block Groups__item"
                  :class="{ 'Groups__item--active': element.id == selectedGroup }"
                  :style="element.id == selectedGroup ? activeItemStyle : {}"
                  :to="{ name: 'home', params: { id: element.id } }"
                  @contextmenu.prevent="handleContextMenu($event, element)"
                >
                  {{ element.title }}
                </router-link>
              </template>
            </draggable>
          </div>
        </transition>
      </div>
      <div class="Search">
        <div class="HeaderButton">
          <sl-icon
            class="shrink-0 p-2 text-lg opacity-75 hover:opacity-100 cursor-pointer"
            library="feather"
            name="search"
            @click="focusSearch"
          ></sl-icon>
        </div>
        <vue3-simple-typeahead
          ref="searchInput"
          class="Search__input"
          :class="{ 'Search__input--open': searchVisible || query }"
          :item-projection="(group) => group.title"
          :items="groups"
          :min-input-length="1"
          :placeholder="$i18n('search')"
          :select-on-tab="false"
          @keydown.esc.stop="clearSearch"
          @keydown.tab.stop="onTabKeyEventHandler"
          @onBlur="onBlurEventHandler"
          @onFocus="onFocusEventHandler"
          @onInput="({ input }) => $emit('update:query', input)"
          @selectItem="onSelectItemEventHandler"
        ></vue3-simple-typeahead>
        <div v-if="searchVisible && !query" class="Search__shortcut"></div>
      </div>
      <div class="Settings HeaderButton" @click="$emit('openSettings')">
        <sl-icon
          class="text-lg opacity-75 hover:opacity-100"
          library="feather"
          name="menu"
          :style="iconStyle"
        ></sl-icon>
      </div>
    </div>
    <div v-else-if="groups.length == 1" class="relative w-full">
      <div class="SettingsIcon" label="Settings" :style="headerStyle" @click="$emit('openSettings')">
        <sl-icon library="feather" name="menu"></sl-icon>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import { mapGetters } from 'vuex'
export default {
  components: {
    draggable
  },

  props: {
    groups: {
      type: Array,
      default: () => []
    },
    selectedGroup: {
      type: String,
      default: '0'
    },
    preferences: {
      type: Object,
      required: true
    },
    query: {
      type: String,
      required: false,
      default: ''
    }
  },

  emits: ['update:query', 'onContextMenu', 'onReorder', 'onCreateGroup', 'onMoveBookmark', 'onFocusBookmarks'],

  data() {
    return {
      searchVisible: false,
      drag: false,
      visibleGroups: [],
      hiddenGroups: [],
      dropdownOpen: false,
      dragOptions: {
        group: 'groups',
        disabled: false,
        animation: 100,
        delay: 100,
        delayOnTouchOnly: true
      }
    }
  },

  computed: {
    ...mapGetters('theme', ['headerStyle', 'activeItemStyle', 'iconStyle']),

    canReorderGroups() {
      return this.preferences?.orderGroupsBy === 'manual'
    },

    hasGroups() {
      return this.groups.length > 1
    },

    hasHiddenGroups() {
      return this.hiddenGroups.length > 0
    }
  },

  watch: {
    groups: {
      deep: true,
      handler() {
        this.responsiveNavigation()
      }
    }
  },

  mounted() {
    window.addEventListener('resize', this.responsiveNavigation)
    this.responsiveNavigation()
  },

  beforeUnmount() {
    window.removeEventListener('resize', this.responsiveNavigation)
  },

  methods: {
    focusSearch() {
      if (!this.hasGroups) {
        return
      }

      this.clearSearch()
      this.$refs.searchInput.focusInput()
    },

    clearSearch() {
      if (!this.query) {
        this.searchVisible = false
      }
      this.$refs.searchInput.clearInput()
      this.$emit('update:query', null)
    },

    onBlurEventHandler() {
      if (!this.query) {
        this.searchVisible = false
      }
    },

    onFocusEventHandler() {
      this.searchVisible = true
    },

    onSelectItemEventHandler(item) {
      this.searchVisible = false
      this.clearSearch()
      this.$router.push({ name: 'home', params: { id: item.id } })
      setTimeout(() => {
        this.$emit('onFocusBookmarks')
      }, 100)
    },

    onTabKeyEventHandler() {
      this.$emit('onFocusBookmarks')
    },

    responsiveNavigation() {
      if (!this.hasGroups) {
        return
      }

      this.visibleGroups = this.groups
      this.$nextTick(() => {
        this.hiddenGroups = []

        const container = this.$refs.groups.$el
        const groups = container.children
        const availableWidth = container.offsetWidth

        let cutOffIndex = undefined
        let requiredWidth = -16

        groups.forEach((el, index) => {
          requiredWidth += el.offsetWidth + 32
          if (requiredWidth > availableWidth && !cutOffIndex) {
            cutOffIndex = index
          }
        })

        if (cutOffIndex) {
          this.hiddenGroups = this.groups.slice(cutOffIndex)
          this.visibleGroups = this.groups.slice(0, cutOffIndex)
        }
      })
    },

    handleContextMenu($event, group) {
      this.$emit('onContextMenu', $event, { group })
    },

    clearDrop(event) {
      event.target.classList.remove('Groups__item--dropTarget')
    },

    allowDrop(event) {
      try {
        if (event.dataTransfer.types.includes('application/json')) {
          event.target.classList.add('Groups__item--dropTarget')
          event.preventDefault()
        }
      } catch (e) {
        console.error(e)
      }
    },

    handleDrop(event) {
      try {
        if (event.dataTransfer.types.includes('application/json')) {
          const data = JSON.parse(event.dataTransfer.getData('application/json'))
          const target = event.target
          const targetId = target.getAttribute('data-id')
          if (data?.id && targetId) {
            this.$emit('onMoveBookmark', { bookmarkId: data.id, groupId: targetId })
          }
        }
      } catch (e) {
        console.error(e)
      }
      event.target.classList.remove('Groups__item--dropTarget')
    },

    handleReorder() {
      this.$nextTick(() => {
        const sorted = [...this.visibleGroups, ...this.hiddenGroups].map(({ id }, index) => {
          return { id, index }
        })

        this.$emit('onReorder', sorted)
      })
    }
  }
}
</script>

<style lang="postcss" scoped>
.Header {
  @apply px-8 bg-white w-full flex items-start shadow-sm select-none flex-shrink-0 relative z-10;
}
.Search {
  @apply flex-shrink-0 self-center rounded flex items-center relative;

  &__shortcut {
    @apply flex absolute right-0 justify-center items-center w-6 h-6 text-xs rounded border;

    &::after {
      content: '';
      display: block;
      box-sizing: border-box;
      position: absolute;
      border-radius: 2px;
      transform: rotate(25deg);
      width: 1px;
      height: 12px;
      background-color: currentColor;
    }
  }

  :deep(.Search__input) {
    @apply py-1.5 focus:outline-none bg-transparent w-0;
    transition: width 0.2s;
    margin: 0 !important;

    &--open {
      @apply w-36;
    }
  }
}

.Settings {
  @apply flex-shrink-0 self-center flex items-center -mr-4 p-2 cursor-pointer;
}
.SettingsIcon {
  @apply absolute z-10 top-0 right-0 m-6 mt-4 w-10 h-10 rounded-full flex items-center justify-center shadow transition-transform cursor-pointer transform scale-100;
  &:hover {
    @apply transform scale-110;
  }
}
.Groups {
  @apply items-start justify-start flex flex-1 w-auto gap-x-8 overflow-hidden mr-2 flex-wrap pl-4 -ml-4;

  max-height: 57px;

  &--dragging {
    cursor: grabbing !important;
  }

  &__item {
    @apply text-current whitespace-nowrap border-b border-transparent opacity-75 h-full;
    line-height: 3.5rem;
    &.sortable-drag {
      @apply border-0;
    }

    &:before {
      display: none;
    }

    &.sortable-ghost,
    &.sortable-ghost:hover {
      @apply border-0 relative opacity-100 text-black;
      z-index: 1;
      cursor: grabbing;
      &:before {
        @apply block absolute left-0 right-0 bg-gray-100 top-1/2 h-8 transform -translate-y-1/2 -mr-3 -ml-3 rounded-full;
        content: '';
        z-index: -1;
      }
    }

    &:hover {
      opacity: 90;
    }

    &--active {
      @apply border-b opacity-100;
    }

    &--dropTarget {
      @apply relative text-white;
      &:before {
        @apply block absolute left-0 right-0 bg-primary top-1/2 h-8 transform -translate-y-1/2 -mr-3 -ml-3 rounded-full;
        content: '';
        z-index: -1;
      }
    }
  }
}
.HiddenGroups {
  @apply opacity-100 relative self-center h-14 flex items-center;

  &__toggle {
    @apply w-8 h-8 rounded-full flex items-center justify-center cursor-pointer opacity-0;
    background: rgba(0, 0, 0, 0.05);

    &--visible {
      @apply opacity-100;
    }

    &:hover {
      background: rgba(0, 0, 0, 0.1);
    }
  }

  &__dropdown {
    @apply bg-white shadow z-50 rounded rounded-t-none overflow-y-auto overflow-x-hidden w-64 right-0 absolute transform-gpu;
    top: 100%;
    max-height: 75vh;

    .Groups__item {
      @apply block px-4 py-2;

      &--active {
        @apply border-b-0 border-l;
      }
    }
  }
  .Groups__item {
    line-height: 1.25rem;
  }
}

.dropdown-enter-active,
.dropdown-leave-active {
  transition: all 0.16s cubic-bezier(0.4, 0, 0.2, 1);
}

.dropdown-enter-from,
.dropdown-leave-to {
  opacity: 0;
  transform: translateY(-0.2rem);
}

.HeaderButton {
  @apply w-8 h-8 flex items-center justify-center rounded transition-colors;
  &:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }
}
</style>
