import difference from 'lodash/difference'

const SyncParserService = {
  parse({ response, localBookmarks, localGroups }) {
    const remoteBookmarksIds = response.dials.map(({ id }) => Number(id))
    const remoteGroupsIds = response.groups.map(({ id }) => Number(id))
    const localBookmarksIds = localBookmarks.map(({ id }) => Number(id))
    const localGroupsIds = localGroups.map(({ id }) => Number(id)).filter((id) => id > 0)

    const newBookmarks = difference(remoteBookmarksIds, localBookmarksIds)
    const removedBookmarks = difference(localBookmarksIds, remoteBookmarksIds)
    const newGroups = difference(remoteGroupsIds, localGroupsIds)
    const removedGroups = difference(localGroupsIds, remoteGroupsIds)

    return {
      preferences: response.preferences,
      bookmarks: {
        toInsert: response.dials.filter(({ id }) => newBookmarks.includes(Number(id))),
        toEdit: response.dials.filter(({ id }) => !newBookmarks.includes(Number(id))),
        toRemove: localBookmarks.filter(({ id }) => removedBookmarks.includes(Number(id)))
      },
      groups: {
        toInsert: response.groups.filter(({ id }) => newGroups.includes(Number(id))),
        toEdit: response.groups.filter(({ id }) => !newGroups.includes(Number(id))),
        toRemove: localGroups.filter(({ id }) => removedGroups.includes(Number(id)))
      }
    }
  }
}

export default SyncParserService
