<template>
  <div class="Home" @contextmenu="onPageContextMenu">
    <div class="Background" :style="pageWrapperBackground"></div>
    <div class="BackgroundColor" :style="pageWrapperBackgroundColor"></div>

    <Header
      v-if="headerType === 'Header'"
      ref="header"
      :groups="visibleGroups"
      :preferences="preferences"
      :query="query"
      :selected-group="selectedGroup"
      @onContextMenu="onGroupContextMenu"
      @onCreateGroup="addGroupModal"
      @onFocusBookmarks="focusBookmarks"
      @onMoveBookmark="moveBookmarkToGroup"
      @onReorder="reorderGroups"
      @openSettings="openSettings"
      @update:query="search"
    />

    <SimpleHeader
      v-else
      ref="simpleHeader"
      :groups="visibleGroups"
      :preferences="preferences"
      :query="query"
      :selected-group="selectedGroup"
      @onContextMenu="onGroupContextMenu"
      @onCreateGroup="addGroupModal"
      @onEditGroup="editCurrentGroup"
      @onFocusBookmarks="focusBookmarks"
      @onMoveBookmark="moveBookmarkToGroup"
      @onReorder="reorderGroups"
      @openSettings="openSettings"
      @update:query="search"
    />

    <Bookmarks
      ref="bookmarks"
      :bookmarks="bookmarkResults"
      :can-reorder-bookmarks="canReorderBookmarks"
      :disabled="disabled"
      :groups="visibleGroups"
      :header-type="headerType"
      :is-premium="isPremium"
      :preferences="preferences"
      :query="query"
      :reloading-bookmark-id="reloadingBookmarkId"
      @onContextMenu="onBookmarkContextMenu"
      @onCreateBookmark="addBookmarkModal()"
      @onEdit="onEdit"
      @onOpenGroup="openGroup"
      @onReorder="reorderBookmarks"
      @onSelect="onSelected"
      @onVisit="handleBookmarkVisit"
    />
    <Sidebar
      v-if="preferences.sidebar || preferences.sidebarBookmarks"
      :preferences="preferences"
      @onUpdatePreferences="updatePreferences"
    />

    <div v-if="!bookmarkResults.length && !isLoading && !query" class="flex-1" :style="textStyle">
      <div class="flex flex-col justify-center items-center min-h-screen">
        <AddButton class="w-64" :label="$i18n('newtab_add_new_dial')" large @click="addBookmarkModal()" />
      </div>
    </div>

    <context-menu ref="groupMenu" @ctx-open="(data) => (groupContextMenuData = data)">
      <div class="ContextMenu">
        <div class="ContextMenu__item" @click="openAllLinks()">
          <sl-icon class="ContextMenu__icon" library="feather" name="arrow-up-right" />
          <span>{{ $i18n('newtab_open_all_links') }}</span>
        </div>
        <div class="ContextMenu__separator"></div>
        <div class="ContextMenu__item" @click="editGroupModal()">
          <sl-icon class="ContextMenu__icon" library="feather" name="edit-2" />
          <span>{{ $i18n('edit') }}</span>
        </div>
        <div class="ContextMenu__item" @click="confirmDeleteGroup()">
          <sl-icon class="ContextMenu__icon" library="feather" name="x-circle" />
          <span>{{ $i18n('remove') }}</span>
        </div>
      </div>
    </context-menu>

    <context-menu ref="bookmarkMenu" @ctx-open="(data) => (bookmarkContextMenuData = data)">
      <div class="ContextMenu">
        <div class="ContextMenu__item" @click="openInNewTab()">
          <sl-icon class="ContextMenu__icon" library="feather" name="arrow-up-right" />
          <span>{{ $i18n('newtab_open_in_new_tab') }}</span>
        </div>
        <div class="ContextMenu__separator"></div>
        <div class="ContextMenu__item" @click="editBookmarkModal()">
          <sl-icon class="ContextMenu__icon" library="feather" name="edit-2" />
          <span>{{ $i18n('edit') }}</span>
        </div>
        <div class="ContextMenu__item" @click="copyBookmarkURL()">
          <sl-icon class="ContextMenu__icon" library="feather" name="copy" />
          <span>{{ $i18n('copy_url') }}</span>
        </div>
        <div
          v-if="canReloadThumbnail"
          class="ContextMenu__item"
          @click="reloadBookmarkThumbnail(bookmarkContextMenuData?.bookmark)"
        >
          <sl-icon class="ContextMenu__icon" library="feather" name="refresh-cw" />
          <span>{{ $i18n('newtab_reload_thumbnail') }}</span>
        </div>
        <div class="ContextMenu__item" @click="confirmDeleteBookmark()">
          <sl-icon class="ContextMenu__icon" library="feather" name="x-circle" />
          <span>{{ $i18n('remove') }}</span>
        </div>
      </div>
    </context-menu>

    <context-menu ref="pageMenu">
      <div class="ContextMenu ContextMenu--large">
        <div class="ContextMenu__item" @click="addBookmarkModal()">
          <sl-icon class="ContextMenu__icon" library="feather" name="plus" />
          <span>{{ $i18n('newtab_add_new_dial') }}</span>
        </div>
        <div class="ContextMenu__item" @click="disabled = true">
          <sl-icon class="ContextMenu__icon" library="feather" name="grid" />
          <span>{{ `${$i18n('edit')} ${$i18n('bookmarks').toLowerCase()}` }}</span>
        </div>
        <div class="ContextMenu__separator"></div>
        <div class="ContextMenu__item" @click="addGroupModal()">
          <sl-icon class="ContextMenu__icon" library="feather" name="folder" />
          <span>{{ $i18n('newtab_create_group') }}</span>
        </div>
        <div class="ContextMenu__separator"></div>
        <div class="ContextMenu__item" @click="openSettings()">
          <sl-icon class="ContextMenu__icon" library="feather" name="settings" />
          <span>{{ $i18n('options') }}</span>
        </div>
        <div class="ContextMenu__item" @click="openHelp()">
          <sl-icon class="ContextMenu__icon" library="feather" name="help-circle" />
          <span>{{ $i18n('help') }}</span>
        </div>
      </div>
    </context-menu>

    <BookmarkModal
      ref="bookmarkModal"
      :bookmark="bookmarkContextMenuData.bookmark"
      :is-freemium="isFreemium"
      :is-premium="isPremium"
      @submit="addOrEditBookmark"
      @submitMultiple="addBookmarks"
      @upgrade="$refs.preferencesModal.show('account')"
    />
    <GroupModal
      ref="groupModal"
      :group="groupContextMenuData.group"
      :is-legacy-plan="isLegacyPlan"
      :is-premium="isPremium"
      @delete="confirmDeleteGroup"
      @submit="addOrEditGroup"
      @upgrade="$refs.preferencesModal.show('account')"
    />
    <PreferencesModal
      ref="preferencesModal"
      :bookmarks-count="bookmarksCount"
      :groups="groups"
      :hidden-groups="hiddenGroups"
      :is-freemium="isFreemium"
      :is-legacy-plan="isLegacyPlan"
      :is-premium="isPremium"
      :preferences="preferences"
      @onChange="updatePreferences"
      @onChangeHiddenGroups="updateHiddenGroups"
      @onCreateGroup="addGroupModal"
    />
    <ConfirmationModal ref="confirmationModal" />
    <DiscountModal
      ref="discountModal"
      :bookmarks-count="bookmarksCount"
      @upgrade="$refs.preferencesModal.show('account')"
    />
    <OnboardingModal
      ref="onboardingModal"
      :preferences="preferences"
      @onOpenPreferences="$refs.preferencesModal.show('theme')"
      @onSignIn="$refs.preferencesModal.show('sign-in')"
      @onUpdatePreferences="updatePreferences"
    />

    <div
      v-if="showUpgradeButton"
      class="fixed right-0 bottom-0 py-0.5 px-2 mr-6 mb-5 text-gray-900 !hover:bg-gray-300 rounded-lg cursor-pointer z-99"
      style="background: rgba(255, 255, 255, 0.75); font-size: 11px"
      @click="$refs.preferencesModal.show('account')"
    >
      Upgrade
    </div>

    <div
      v-if="showPastDueButton"
      class="fixed right-0 bottom-0 py-0.5 px-2 mr-6 mb-5 text-white bg-red-400 !hover:bg-gray-300 rounded-lg cursor-pointer z-99"
      style="font-size: 11px"
      @click="$refs.preferencesModal.show('account')"
    >
      Past Due
    </div>

    <transition name="fade">
      <div v-if="disabled" class="BookmarkActions">
        <div class="flex items-center space-x-2 font-medium text-black">
          {{ selectedBookmarks.length }} {{ $i18n('bookmarks') }}
        </div>
        <div class="flex items-center space-x-3">
          <sl-dropdown v-if="groups.length > 1">
            <sl-button slot="trigger" outline variant="primary">
              <sl-icon class="mr-2" library="feather" name="arrow-right"></sl-icon>
              {{ $i18n('move_to') }}...
            </sl-button>

            <sl-menu>
              <sl-menu-item
                v-for="group in groups"
                :key="group.id"
                :value="group.id"
                @click="confirmBulkMoveBookmarks(group.id)"
              >
                {{ group.title }}
              </sl-menu-item>
            </sl-menu>
          </sl-dropdown>

          <sl-button
            v-if="canReloadBookmarkThumbnails"
            slot="trigger"
            outline
            variant="primary"
            @click="bulkReloadThumbnails()"
          >
            <sl-icon class="mr-2" library="feather" name="refresh-cw"></sl-icon>
            {{ $i18n('newtab_reload_thumbnail') }}...
          </sl-button>

          <sl-button outline variant="primary" @click="confirmBulkDeleteBookmarks()">
            <sl-icon class="mr-2" library="feather" name="trash-2"></sl-icon>
            {{ $i18n('delete') }}
          </sl-button>

          <span class="px-2 text-muted">or</span>

          <sl-button
            variant="neutral"
            @click="
              () => {
                selectBookmarks(false)
                disabled = false
              }
            "
          >
            {{ $i18n('cancel') }}
          </sl-button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Selecto from 'selecto'
import { mapActions, mapGetters } from 'vuex'

import AddButton from '@/components/AddButton'
import Bookmarks from '@/components/Bookmarks'
import ContextMenu from '@/components/ContextMenu'
import Header from '@/components/Header'
import BookmarkModal from '@/components/Modals/BookmarkModal'
import ConfirmationModal from '@/components/Modals/ConfirmationModal'
import DiscountModal from '@/components/Modals/DiscountModal'
import GroupModal from '@/components/Modals/GroupModal'
import OnboardingModal from '@/components/Modals/OnboardingModal'
import PreferencesModal from '@/components/Modals/PreferencesModal'
import Sidebar from '@/components/Sidebar'
import SimpleHeader from '@/components/SimpleHeader'
import StorageService from '@/services/StorageService'
import { isFirefox } from '@/utils/browser'
import createPageScreenshot from '@/utils/createPageScreenshot'

export default {
  components: {
    AddButton,
    Bookmarks,
    BookmarkModal,
    DiscountModal,
    Header,
    SimpleHeader,
    Sidebar,
    GroupModal,
    ContextMenu,
    PreferencesModal,
    ConfirmationModal,
    OnboardingModal
  },

  props: {
    id: {
      type: String,
      required: false
    },
    showOnboarding: {
      type: Boolean,
      required: false,
      default: false
    },
    showPreferences: {
      type: Boolean,
      required: false,
      default: false
    },
    preferences: {
      type: Object,
      required: true
    },
    hiddenGroups: {
      type: Array,
      required: true,
      default: () => []
    },
    isFreemium: {
      type: Boolean,
      required: false,
      default: false
    },
    isPremium: {
      type: Boolean,
      required: false,
      default: false
    },
    isLegacyPlan: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  data() {
    return {
      bookmarkContextMenuData: {},
      groupContextMenuData: {},
      disabled: false,
      lastSelectedIndex: 0,
      reloadingBookmarkId: null,
      selecto: null
    }
  },

  computed: {
    ...mapGetters('bookmarks', [
      'bookmarks',
      'groups',
      'visibleGroups',
      'selectedGroup',
      'searchResults',
      'bookmarksCount',
      'query',
      'isLoading',
      'showDiscount'
    ]),

    ...mapGetters('theme', ['pageWrapperBackground', 'pageWrapperBackgroundColor', 'textStyle']),

    canReloadThumbnail() {
      return !this.bookmarkContextMenuData?.bookmark?.thumbnail
    },

    canReloadBookmarkThumbnails() {
      return this.selectedBookmarks.some(({ thumbnail }) => !thumbnail)
    },

    headerType() {
      return this.preferences.headerStyle === 'compact' ? 'SimpleHeader' : 'Header'
    },

    canReorderBookmarks() {
      return this.preferences.orderBy === 'manual' && !this.query
    },

    bookmarkResults() {
      return this.query ? this.searchResults : this.bookmarks
    },

    selectedBookmarks() {
      return this.bookmarkResults.filter(({ selected }) => selected)
    },

    showUpgradeButton() {
      return !StorageService.load('isPremium', false)
    },

    showPastDueButton() {
      return StorageService.load('isPastDue', false)
    }
  },

  watch: {
    id: {
      immediate: false,
      handler() {
        this.search(null)
        this.setSelectedGroup(this.id)
        this.initSelectionHandler()
      }
    },

    selectedBookmarks: {
      immediate: false,
      handler(value) {
        this.disabled = Boolean(value.length)
        if (this.$refs.simpleHeader) {
          this.$refs.simpleHeader.$el.classList.toggle('SimpleHeader--hidden', Boolean(value.length))
        }
      }
    },

    showDiscount: {
      handler(value) {
        if (value === true) {
          this.$refs.discountModal.show()
        }
      }
    }
  },

  async mounted() {
    this.initOnboarding()
    this.initSelectionHandler()
    window.addEventListener('keydown', this.keyboardListener)

    // Reload on Win if it fails to initialize
    this.$nextTick(() => {
      if (this.showPreferences) {
        this.openSettings()
      }

      if (navigator.appVersion.indexOf('Win') != -1) {
        setTimeout(() => {
          if (this.isLoading === true) {
            this.init()
          }
        }, 1600)
      }
    })

    if (this.$refs.bookmarks && this.$refs.simpleHeader) {
      this.$refs.bookmarks.$el.addEventListener('scroll', this.handleScroll)
    }
  },

  unmounted() {
    window.removeEventListener('keydown', this.keyboardListener)

    if (this.$refs.bookmarks && this.$refs.simpleHeader) {
      this.$refs.bookmarks.$el.removeEventListener('scroll', this.handleScroll)
    }
  },

  methods: {
    ...mapActions('bookmarks', [
      'addThumbnail',
      'setSelectedGroup',
      'searchBookmarks',
      'createBookmark',
      'editBookmark',
      'destroyBookmark',
      'bulkDestroyBookmarks',
      'bulkMoveBookmarks',
      'createGroup',
      'editGroup',
      'destroyGroup',
      'updateBookmarksPosition',
      'updateGroupsPosition',
      'addBookmarkVisit',
      'init'
    ]),

    ...mapActions('preferences', ['updatePreferences', 'updateHiddenGroups']),

    handleScroll(e) {
      // Hide SimpleHeader if bookmarks ref is scrolled more than 100px
      if (this.$refs.simpleHeader) {
        this.$refs.simpleHeader.$el.classList.toggle('SimpleHeader--hidden', e.target.scrollTop > 480)
      }
    },

    onBookmarkContextMenu($event, { bookmark }) {
      this.$refs.bookmarkMenu.open($event, { bookmark })
      $event.stopPropagation()
    },

    onPageContextMenu($event) {
      this.$refs.pageMenu.open($event)
    },

    onGroupContextMenu($event, { group }) {
      this.$refs.groupMenu.open($event, { group })
      $event.stopPropagation()
    },

    onEdit($event, bookmark) {
      this.bookmarkContextMenuData = { bookmark }
      this.editBookmarkModal()
    },

    // Bookmarks

    addBookmarkModal() {
      this.bookmarkContextMenuData = { bookmark: { idgroup: this.selectedGroup } }
      this.$nextTick(() => {
        this.$refs.bookmarkModal.show()
      })
    },

    editBookmarkModal() {
      this.$nextTick(() => {
        this.$refs.bookmarkModal.show()
      })
    },

    async reloadBookmarkThumbnail({ id, url }) {
      if (!id || !url) {
        return
      }

      if (isFirefox()) {
        try {
          await browser.permissions.request({ origins: ['<all_urls>'] })
        } catch {
          // no permission
        }
      }

      try {
        this.reloadingBookmarkId = id
        const image = await createPageScreenshot(url)
        await this.addThumbnail({ id, url, image })
        this.init()
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e)
      } finally {
        this.reloadingBookmarkId = null
      }
    },

    copyBookmarkURL() {
      const { url } = this.bookmarkContextMenuData?.bookmark
      navigator.clipboard.writeText(url || '')
    },

    async addOrEditBookmark(bookmark) {
      let result
      if (bookmark.id) {
        result = await this.editBookmark(bookmark)
      } else {
        result = await this.createBookmark(bookmark)
      }

      // Capture bookmark thumbnail if not present
      if (result && !bookmark?.thumbnail && !bookmark?.dataThumbnail) {
        await this.reloadBookmarkThumbnail(result)
        this.$refs.bookmarkModal.hide()
      }
    },

    async addBookmarks(bookmarks) {
      let result
      for (const bookmark of bookmarks) {
        result = await this.createBookmark(bookmark)

        if (result && !result?.thumbnail && !result?.dataThumbnail) {
          await this.reloadBookmarkThumbnail(result)
        }
      }

      this.$refs.bookmarkModal.hide()
    },

    openInNewTab() {
      const { url } = this.bookmarkContextMenuData.bookmark
      browser.tabs.create({ url, active: false })
    },

    // Groups

    addGroupModal() {
      this.groupContextMenuData = { group: {} }
      this.$nextTick(() => {
        this.$refs.groupModal.show()
      })
    },

    editGroupModal() {
      this.$nextTick(() => {
        this.$refs.groupModal.show()
      })
    },

    editCurrentGroup() {
      const currentGroup = this.groups.find((group) => group.id == this.selectedGroup)
      if (!currentGroup) {
        return
      }

      this.groupContextMenuData = { group: { ...currentGroup } }

      this.$nextTick(() => {
        this.$refs.groupModal.show()
      })
    },

    async addOrEditGroup(group) {
      if (typeof group.id !== 'undefined') {
        this.editGroup(group)
      } else {
        const { id } = await this.createGroup(group)
        this.$router.push({ name: 'home', params: { id } })
      }
    },

    async openAllLinks() {
      const currentGroup = this.selectedGroup
      const { id } = this.groupContextMenuData.group
      await this.setSelectedGroup(id)
      const groupBookmarks = [...this.bookmarks]
      await this.setSelectedGroup(currentGroup)
      groupBookmarks.forEach(({ url }) => {
        browser.tabs.create({ url, active: false })
      })
    },

    reorderBookmarks(data) {
      // XXX: Hack to fix the issue with the bookmarks not being moved from one group to another when also rearanging them
      setTimeout(() => {
        this.updateBookmarksPosition(data)
      }, 50)
    },

    reorderGroups(data) {
      this.updateGroupsPosition(data)
    },

    search(e) {
      this.searchBookmarks(e)
    },

    openSettings() {
      this.$refs.preferencesModal.show()
    },

    openHelp() {
      window.open('https://www.speeddial2.com/faq')
    },

    openGroup($event, id) {
      this.search(null)
      this.$router.push({ name: 'home', params: { id } })
    },

    focusBookmarks() {
      document.querySelector('.Bookmark:first-of-type')?.focus({ preventScroll: true })
    },

    initOnboarding() {
      const welcomeScreen = this.showOnboarding && StorageService.load('onboardingComplete') === undefined
      const onboardingStarted = StorageService.load('onboardingComplete') === false

      if (welcomeScreen || onboardingStarted) {
        StorageService.persist('onboardingComplete', false)
        this.$refs.onboardingModal.show()
      }
    },

    initSelectionHandler() {
      this.selecto?.destroy()
      this.selecto = new Selecto({
        // The container to add a selection element
        container: this.$refs.bookmarks.$el,
        // Selecto's root container (No transformed container. (default: null)
        rootContainer: null,
        boundContainer: true,
        // The area to drag selection element (default: container)
        // dragContainer: Element,
        // Targets to select. You can register a queryselector or an Element.
        selectableTargets: document.getElementsByClassName('Bookmark'),
        // Whether to select by click (default: true)
        selectByClick: false,
        // Whether to select from the target inside (default: true)
        selectFromInside: false,
        // After the select, whether to select the next target with the selected target (deselected if the target is selected again).
        continueSelect: false,
        // Determines which key to continue selecting the next target via keydown and keyup.
        toggleContinueSelect: 'shift',
        // The container for keydown and keyup events
        keyContainer: window,
        // The rate at which the target overlaps the drag area to be selected. (default: 100)
        hitRate: 0,
        checkInput: true
      })

      this.selecto.on('dragStart', (e) => {
        if (e?.inputEvent && !e.inputEvent.shiftKey) {
          this.selectBookmarks(false)
        }
      })

      this.selecto.on('select', (e) => {
        e.added.forEach((el) => {
          const bookmark = this.bookmarkResults.find(({ id }) => id == el.dataset.id)
          if (!bookmark.selected) {
            bookmark.selected = true
          }
        })
      })
    },

    keyboardListener(e) {
      const modalRefs = ['bookmarkModal', 'groupModal', 'preferencesModal', 'confirmationModal']
      if (modalRefs.some((ref) => this.$refs[ref].$el.open)) {
        return
      }

      // Keyboard navigation through bookmarks
      if (
        window.document.activeElement?.className === 'Bookmark' &&
        ['ArrowRight', 'ArrowLeft', 'ArrowUp', 'ArrowDown'].includes(e.key)
      ) {
        const activeElement = window.document.activeElement
        const currentIndex = [...activeElement?.parentNode?.children].indexOf(activeElement)

        if (currentIndex > -1) {
          let nextIndex

          if (e.key === 'ArrowRight') {
            nextIndex = currentIndex + 1
          }
          if (e.key === 'ArrowLeft') {
            nextIndex = currentIndex - 1
          }
          if (e.key === 'ArrowDown') {
            nextIndex = currentIndex + this.preferences.columns
          }
          if (e.key === 'ArrowUp') {
            nextIndex = currentIndex - this.preferences.columns
          }
          if (nextIndex >= 0 && nextIndex < this.bookmarkResults.length) {
            activeElement.parentNode.children[nextIndex]?.focus()
            e.preventDefault()
            return
          }
        }
      }

      // no keyboard event action on input select and textarea
      if (
        e.target instanceof HTMLInputElement === true ||
        e.target instanceof HTMLSelectElement === true ||
        e.target instanceof HTMLTextAreaElement === true
      ) {
        return
      }

      if (e.key === '/') {
        this.$refs.header.focusSearch()
        e.preventDefault()
        return
      }

      if (e.key === 'f' && (e.metaKey || e.ctrlKey)) {
        this.$refs.header.focusSearch()
        e.preventDefault()
        e.stopPropagation()
        return
      }

      if (!this.bookmarkResults.length) {
        return
      }

      if (e.keyCode == 27 && this.disabled) {
        this.selectBookmarks(false)
      }

      if (e.keyCode == 27 && window.document.activeElement?.className === 'Bookmark') {
        window.document.activeElement?.blur()
      }

      if ((e.metaKey || e.ctrlKey) && (e.keyCode == 97 || e.keyCode == 65)) {
        this.selectBookmarks(true)
      }
    },

    onSelected(e, { bookmark }) {
      const currentIndex = this.bookmarkResults.findIndex(({ id }) => id == bookmark.id)
      if (e.shiftKey) {
        for (var i = this.lastSelectedIndex; i <= currentIndex; i++) {
          this.bookmarkResults[i].selected = true
        }
      } else {
        bookmark.selected = !bookmark.selected
      }
      this.lastSelectedIndex = currentIndex
    },

    selectBookmarks(selected) {
      for (var i = 0; i < this.bookmarkResults.length; i++) {
        this.bookmarkResults[i].selected = selected
      }
    },

    handleBookmarkVisit(e, bookmarkId) {
      this.addBookmarkVisit(bookmarkId)
    },

    // Confirmations

    async confirmDeleteBookmark() {
      const result = await this.$refs.confirmationModal.show({
        data: this.bookmarkContextMenuData.bookmark,
        options: {
          title: this.$i18n('newtab_remove_question'),
          message: this.$i18n('newtab_dial_remove_warning')
        }
      })
      if (result) {
        await this.destroyBookmark(result)
      }
    },

    async confirmDeleteGroup(group) {
      const result = await this.$refs.confirmationModal.show({
        data: group || this.groupContextMenuData.group,
        options: {
          title: this.$i18n('newtab_remove_group'),
          message: this.$i18n('newtab_group_remove_warning')
        }
      })
      if (result) {
        await this.destroyGroup(result)
        this.$router.push({ name: 'home', params: { id: 0 } })
      }
    },

    async confirmBulkDeleteBookmarks() {
      const result = await this.$refs.confirmationModal.show({
        data: this.selectedBookmarks,
        options: {
          title: `Delete ${this.selectedBookmarks.length} bookmarks?`,
          message: 'Do you really wish to remove selected bookmarks?'
        }
      })
      if (result) {
        const ids = result.map(({ id }) => id)
        await this.bulkDestroyBookmarks(ids)
      }
    },

    async confirmBulkMoveBookmarks(groupId) {
      const result = await this.$refs.confirmationModal.show({
        data: this.selectedBookmarks,
        options: {
          title: `Move ${this.selectedBookmarks.length} bookmarks?`,
          message: 'Do you really wish to move selected bookmarks?'
        }
      })
      if (result) {
        const ids = result.map(({ id }) => id)
        await this.bulkMoveBookmarks({ ids, groupId })
      }
    },

    async moveBookmarkToGroup({ bookmarkId, groupId }) {
      await this.bulkMoveBookmarks({ ids: [Number(bookmarkId)], groupId: Number(groupId) })
    },

    async bulkReloadThumbnails() {
      for (const bookmark of this.selectedBookmarks) {
        if (!bookmark.thumbnail) {
          this.reloadingBookmarkId = bookmark.id
          await this.reloadBookmarkThumbnail(bookmark)
        }
      }
      this.reloadingBookmarkId = null
    }
  }
}
</script>

