import { createStore } from 'vuex'

import bookmarks from '@/store/modules/bookmarks'
import browserBookmarks from '@/store/modules/browserBookmarks'
import preferences from '@/store/modules/preferences'
import theme from '@/store/modules/theme'

const store = createStore({
  actions: {
    async init({ dispatch }) {
      dispatch('bookmarks/init', null, { root: true })
      dispatch('preferences/init', null, { root: true })
    }
  },

  modules: {
    bookmarks,
    preferences,
    theme,
    browserBookmarks
  }
})

export default store
