<template>
  <div class="Wrapper" :class="wrapperClass">
    <div v-if="query" class="SearchWrapper">
      <div class="Search" :style="{ ...containerStyle, ...textStyle }">
        <div class="flex items-center space-x-2 title-3">
          <sl-icon library="feather" name="search"></sl-icon>
          <span class="flex items-center">
            {{ bookmarks.length ? bookmarks.length : 'No' }} results for
            {{ query }}
          </span>
        </div>
      </div>
    </div>

    <draggable
      v-show="bookmarks.length"
      v-model="bookmarks"
      v-bind="dragOptions"
      class="Bookmarks"
      :class="{ 'pt-8 pb-20': groups.length > 1, 'py-20': groups.length <= 1 }"
      :disabled="disabled || !canReorderBookmarks"
      item-key="id"
      :style="bookmarksStyle"
      @update="handleReorder"
    >
      <template #item="{ element, index }">
        <Bookmark
          :bookmark="element"
          :disabled="disabled"
          :is-premium="isPremium"
          :is-reloading="element?.id === reloadingBookmarkId"
          :is-search-result="query"
          :preferences="preferences"
          :tabindex="index + 1"
          @onContextMenu="handleContextMenu"
          @onEdit="handleEdit"
          @onOpenGroup="handleOpenGroup"
          @onSelected="handleSelect"
          @onVisit="handleVisit"
        />
      </template>
      <template v-if="preferences.showAddButton" #footer>
        <AddButton @click="$emit('onCreateBookmark')" />
      </template>
    </draggable>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import { mapGetters } from 'vuex'

import AddButton from '@/components/AddButton'
import Bookmark from '@/components/Bookmark'

export default {
  components: {
    AddButton,
    Bookmark,
    draggable
  },
  props: {
    bookmarks: {
      type: Array,
      default: () => []
    },
    groups: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    headerType: {
      type: String,
      default: 'Header'
    },
    canReorderBookmarks: {
      type: Boolean,
      default: false
    },
    query: {
      type: String,
      required: false
    },
    isPremium: {
      type: Boolean,
      required: false,
      default: false
    },
    preferences: {
      type: Object,
      default: () => {},
      required: false
    },
    reloadingBookmarkId: {
      type: String,
      required: false
    }
  },

  emits: ['onContextMenu', 'onReorder', 'onSelect', 'onVisit', 'onCreateBookmark', 'onOpenGroup', 'onEdit'],

  data() {
    return {
      dragOptions: {
        animation: 200,
        delay: 100,
        delayOnTouchOnly: true
      }
    }
  },

  computed: {
    ...mapGetters('theme', ['bookmarksStyle', 'containerStyle', 'textStyle']),

    wrapperClass() {
      const classes = []
      const alignToTop = this.query || !this.preferences?.centeredLayout

      classes.push(alignToTop ? 'items-start' : 'items-center')

      if (this.headerType === 'SimpleHeader') {
        classes.push('pt-16')
      }

      return classes.join(' ')
    }
  },

  methods: {
    handleContextMenu($event, { bookmark }) {
      this.$emit('onContextMenu', $event, { bookmark })
    },

    handleSelect($event, { bookmark }) {
      this.$emit('onSelect', $event, { bookmark })
    },

    handleVisit($event, bookmarkId) {
      this.$emit('onVisit', $event, bookmarkId)
    },

    handleEdit($event, bookmark) {
      this.$emit('onEdit', $event, bookmark)
    },

    handleOpenGroup($event, bookmarkId) {
      this.$emit('onOpenGroup', $event, bookmarkId)
    },

    handleReorder() {
      const sorted = this.bookmarks.map(({ id }, index) => {
        return { id, index }
      })

      this.$emit('onReorder', sorted)
    }
  }
}
</script>

<style lang="postcss" scoped>
.Wrapper {
  @apply relative w-full flex flex-grow flex-wrap justify-center overflow-auto px-8;
}
.SearchWrapper {
  @apply relative w-full flex flex-shrink-0 flex-wrap justify-center overflow-auto px-12;
}
.Search {
  @apply text-left mt-8;

  width: 80%;
  max-width: 3200px;
}

.Bookmarks {
  @apply select-none grid grid-cols-4 gap-6;

  width: 80%;
  max-width: 3200px;
}
.Bookmark.sortable-ghost {
  opacity: 0.5;
}
.Bookmark.sortable-drag {
  @apply shadow-none;
}
</style>
