<template>
  <div class="App" :class="effectiveTheme">
    <router-view
      :hidden-groups="hiddenGroups"
      :is-freemium="isFreemium"
      :is-legacy-plan="isLegacyPlan"
      :is-premium="isPremium"
      :preferences="preferences"
    ></router-view>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters('bookmarks', ['isPremium', 'isLegacyPlan', 'isFreemium']),

    ...mapGetters('preferences', ['preferences', 'hiddenGroups']),

    ...mapGetters('theme', ['effectiveTheme'])
  },

  mounted() {
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', this.handleColorSchemeChange)
    this.initializeTheme()
  },

  unmounted() {
    window.matchMedia('(prefers-color-scheme: dark)').removeEventListener('change', this.handleColorSchemeChange)
  },

  methods: {
    ...mapActions('theme', ['setDarkOSTheme', 'initializeTheme']),

    handleColorSchemeChange(event) {
      this.setDarkOSTheme(event.matches)
    }
  }
}
</script>
