<template>
  <div class="SidebarPanel">
    <div class="SidebarPanel__header">
      {{ $i18n('recently_closed_tabs') }}
    </div>

    <div v-if="!hasPermission" class="p-6 text-center SidebarPanel__body">
      <h3 class="text-base leading-relaxed text-muted">
        Speed Dial 2 needs your permission to display recently closed tabs.
      </h3>
      <sl-button class="mt-4" @click="requestPermissions()">Allow access</sl-button>
    </div>

    <div v-else class="SidebarPanel__body">
      <a
        v-for="(tab, index) in recentlyClosed"
        :key="index"
        class="flex items-center p-3 space-x-3 text-sm truncate hover:bg-gray-50"
        :href="tab.url"
      >
        <img alt="" class="w-4 h-4 rounded-full" loading="lazy" :src="getLogo(tab.url)" />
        <div class="truncate">{{ tab.title }}</div>
      </a>
    </div>
    <div class="SidebarPanel__footer">
      <sl-button class="w-full" variant="default" @click="openAll">
        <sl-icon slot="prefix" library="feather" name="rotate-ccw"></sl-icon>
        Restore all
      </sl-button>
    </div>
  </div>
</template>

<script>
import uniqBy from 'lodash/uniqBy'

import { isFirefox } from '@/utils/browser'
import { getFaviconURL } from '@/utils/getFaviconURL'

export default {
  data() {
    return {
      hasPermission: true,
      recentlyClosed: []
    }
  },

  beforeMount() {
    this.checkPermissions()
  },

  methods: {
    init() {
      this.recentlyClosed = []

      browser.sessions.getRecentlyClosed().then((sessions) => {
        if (sessions.length) {
          if (sessions[0].window) {
            sessions[0].window.tabs.forEach((tab) => {
              this.recentlyClosed.push({
                title: tab.title,
                domain: new URL(tab.url).hostname,
                url: tab.url
              })
            })
          } else {
            sessions.forEach((session) => {
              if (session.window) {
                session.window.tabs.forEach((tab) => {
                  this.recentlyClosed.push({
                    title: tab.title,
                    domain: new URL(tab.url).hostname,
                    url: tab.url
                  })
                })
              } else {
                this.recentlyClosed.push({
                  title: session.tab.title,
                  domain: new URL(session.tab.url).hostname,
                  url: session.tab.url
                })
              }
            })

            this.recentlyClosed = uniqBy(this.recentlyClosed, 'url')
            this.recentlyClosed = this.recentlyClosed.filter(({ title }) => !title.startsWith('New tab'))
            this.recentlyClosed = this.recentlyClosed.splice(0, 25)
          }
        }
      })
    },

    requestPermissions() {
      browser.permissions
        .request({
          permissions: ['sessions']
        })
        .then((granted) => {
          if (granted) {
            this.hasPermission = granted
            this.init()
          }
        })
    },

    checkPermissions() {
      browser.permissions.contains({ permissions: ['sessions'] }).then((granted) => {
        this.hasPermission = granted

        if (granted) {
          this.init()
        }
      })
    },

    getLogo(url) {
      if (isFirefox()) {
        try {
          const hostname = new URL(url).hostname
          return `https://favicone.com/${hostname}?s=32`
        } catch {
          return 'https://assets.speeddial2.com/assets/empty-favicon.svg'
        }
      } else {
        return getFaviconURL(url)
      }
    },

    openAll() {
      this.recentlyClosed.forEach((tab) => {
        window.open(tab.url)
      })
    }
  }
}
</script>
