// Services
import DatabaseService from '@/services/LocalStorageDatabaseService'
import SyncParserService from '@/services/SyncParserService'
import SyncService from '@/services/SyncService'

const log = (message) => {
  // eslint-disable-next-line no-console
  console.log(`[ ${Date.now()} ] ${message}`)
}

const DataService = {
  async sync(response = null) {
    log('Sync...')

    if (!response) {
      await browser.storage.local.remove('sync')
      response = await SyncService.get()
    }

    if (!response || !('dials' in response)) {
      return
    }

    const localBookmarks = await DatabaseService.getBookmarks()
    const localGroups = await DatabaseService.getGroups()

    const sync = SyncParserService.parse({ response, localBookmarks, localGroups })

    await DatabaseService.bulkDestroyBookmarks(sync.bookmarks.toRemove.map(({ id }) => Number(id)))

    for (const bookmark of sync.bookmarks.toEdit) {
      // TODO Too much work happening here...
      await DatabaseService.editBookmark(bookmark)
    }
    for (const bookmark of sync.bookmarks.toInsert) {
      await DatabaseService.createBookmarkWithId(bookmark)
    }
    for (const group of sync.groups.toRemove) {
      await DatabaseService.destroyGroup(group)
    }
    for (const group of sync.groups.toEdit) {
      await DatabaseService.editGroup(group)
    }
    for (const group of sync.groups.toInsert) {
      await DatabaseService.createGroupWithId(group)
    }

    // sync preferences
    if (sync.preferences) {
      const result = await browser.storage.local.get({ preferences: {} })
      const currentPreferences = result.preferences
      const noSyncPreferences = ['username', 'password', 'columns', 'spacing', 'maxWidth']
      const preferences = {
        ...sync.preferences
      }

      noSyncPreferences.forEach((key) => {
        preferences[key] = currentPreferences[key]
      })

      await browser.storage.local.set({ preferences })
    }

    log('Done: Sync')
  },

  async export() {
    log('Export...')

    const dials = await DatabaseService.getBookmarks()
    const groups = await DatabaseService.getGroups()
    const result = await browser.storage.local.get({ preferences: {} })
    const preferences = result.preferences

    // Filter sensitive information
    delete preferences.username
    delete preferences.password

    log('Done: Export')

    return {
      dials,
      groups,
      preferences
    }
  },

  async import(data) {
    log('Import...')

    if (!data) {
      return false
    }

    const json = JSON.parse(data)

    if (json) {
      await DatabaseService.dropTables()
      await browser.storage.local.remove('hiddenGroups')

      const result = await browser.storage.local.get({ preferences: {} })
      const preferences = result.preferences

      for (const bookmark of Object.values(json.dials)) {
        await DatabaseService.createBookmarkWithId(bookmark)
      }

      for (const group of Object.values(json.groups)) {
        await DatabaseService.createGroupWithId(group)
      }

      // sync preferences
      if (json.preferences) {
        const noSyncPreferences = ['username', 'password', 'columns', 'spacing', 'maxWidth']

        Object.keys(json.preferences).forEach((key) => {
          if (!noSyncPreferences.includes(key)) {
            preferences[key] = json.preferences[key]
          }
        })

        await browser.storage.local.set({ preferences })
      }

      const dials = await DatabaseService.getBookmarks()
      const groups = await DatabaseService.getGroups(false)

      const data = {
        dials,
        groups,
        preferences
      }

      await SyncService.import(data)
    }

    log('Done: Import')
  },

  async backup() {
    log('Backup...')

    const data = await this.export()

    await SyncService.backup(data)

    log('Done: Backup')
  },

  async merge() {
    log('Merge...')

    const data = await this.export()

    await SyncService.merge(data)

    log('Done: Merge')
  }
}

export default DataService
