import DatabaseService from '@/services/LocalStorageDatabaseService'
import StorageService from '@/services/StorageService'
import SyncService from '@/services/SyncService'

const types = {
  SET_APP_LOADING: 'SET_APP_LOADING',
  SET_BOOKMARKS: 'SET_BOOKMARKS',
  SET_BOOKMARKS_COUNT: 'SET_BOOKMARKS_COUNT',
  SET_GROUPS: 'SET_GROUPS',
  SET_SELECTED_GROUP: 'SET_SELECTED_GROUP',
  SEARCH_QUERY_SET: 'SEARCH_QUERY_SET',
  SEARCH_RESULTS_SET: 'SEARCH_RESULTS_SET',
  SET_BOOKMARKS_WITH_NO_THUMBNAIL: 'SET_BOOKMARKS_WITH_NO_THUMBNAIL',
  SET_BOOKMARKS_WITH_THUMBNAIL: 'SET_BOOKMARKS_WITH_THUMBNAIL',
  SET_SHOW_DISCOUNT: 'SET_SHOW_DISCOUNT',
  SET_PROFILE: 'SET_PROFILE',
  SET_FREEMIUM: 'SET_FREEMIUM'
}

const USE_CACHE = true

const getInitialState = () => ({
  groups: USE_CACHE ? StorageService.load('cachedGroups', []) : [],
  bookmarks: USE_CACHE ? StorageService.load('cachedBookmarks', []) : [],
  bookmarksCount: 0,
  searchResults: [],
  selectedGroup: localStorage.getItem('selectedGroup') || '0',
  query: undefined,
  isLoading: false,
  bookmarksWithNoThumbnail: [],
  bookmarksWithThumbnail: [],
  showDiscount: false,
  freemium: undefined,
  profile: undefined
})

const state = getInitialState()

const actions = {
  async init({ commit, dispatch, state, rootState }) {
    commit(types.SET_APP_LOADING, true)

    await dispatch('loadGroups')

    // resolve active group
    if (
      rootState.preferences.preferences.keepActiveGroup &&
      state.selectedGroup &&
      state.groups.find(({ id }) => id == state.selectedGroup)
    ) {
      commit(types.SET_SELECTED_GROUP, state.selectedGroup)
    } else {
      commit(types.SET_SELECTED_GROUP, '0')
    }

    await dispatch('loadBookmarks')

    commit(types.SET_APP_LOADING, false)

    await dispatch('loadProfile')
    await dispatch('initializeDiscount')

    const result = await browser.storage.local.get({ installedOn: undefined })

    commit(types.SET_FREEMIUM, result.installedOn)
  },

  async setSelectedGroup({ commit, dispatch, state }, id) {
    commit(types.SET_SELECTED_GROUP, id)
    localStorage.setItem('selectedGroup', id)

    await dispatch('loadBookmarks')
  },

  async loadGroups({ commit, dispatch, state, rootState }) {
    const orderBy = rootState.preferences?.preferences?.orderGroupsBy
    const groups = await DatabaseService.getGroups(true, orderBy)
    commit(types.SET_GROUPS, groups)
    if (USE_CACHE) {
      try {
        StorageService.persist('cachedGroups', groups)
      } catch {
        StorageService.remove('cachedGroups')
      }
    }
  },

  async loadBookmarks({ commit, dispatch, state, rootState }) {
    const orderBy = rootState.preferences?.preferences?.orderBy
    const bookmarks = await DatabaseService.getBookmarksByGroup(state.selectedGroup, orderBy)
    commit(types.SET_BOOKMARKS, bookmarks)
    const bookmarksCount = await DatabaseService.getBookmarksCount()
    commit(types.SET_BOOKMARKS_COUNT, bookmarksCount)

    if (state.query) {
      dispatch('searchBookmarks', state.query)
    }

    if (USE_CACHE) {
      try {
        if (bookmarks.length <= 32) {
          StorageService.persist('cachedBookmarks', bookmarks)
        } else {
          StorageService.remove('cachedBookmarks')
        }
      } catch {
        StorageService.remove('cachedBookmarks')
      }
    }
  },

  async loadProfile({ commit, dispatch, rootGetters }) {
    if (rootGetters['preferences/username'] && rootGetters['preferences/password']) {
      const profile = await SyncService.getProfile()

      StorageService.persist('isPremium', profile?.active)

      if (profile?.subscription?.status === 'past_due') {
        StorageService.persist('isPastDue', true)
      } else {
        StorageService.persist('isPastDue', false)
      }

      // clear password if profile is not active
      if (profile?.code === -99) {
        dispatch('preferences/updateAttribute', { attribute: 'password', value: '' }, { root: true })
      }

      commit(types.SET_PROFILE, profile)
    } else {
      StorageService.persist('isPremium', false)
      StorageService.persist('isPastDue', false)
    }
  },

  async searchBookmarks({ commit }, query) {
    const searchQuerySet = typeof query === 'string' && query.trim()
    if (searchQuerySet) {
      const bookmarks = await DatabaseService.search(query)
      commit(types.SEARCH_QUERY_SET, query)
      commit(types.SEARCH_RESULTS_SET, bookmarks)
    } else {
      commit(types.SEARCH_QUERY_SET, undefined)
      commit(types.SEARCH_RESULTS_SET, [])
    }
  },

  async createBookmark({ commit, dispatch, state }, bookmark) {
    const result = await DatabaseService.createBookmark(bookmark)

    if (bookmark.dataImage) {
      await dispatch('addThumbnail', { id: result.id, url: bookmark.url, image: bookmark.dataImage })
    }

    dispatch('loadBookmarks')
    SyncService.addBookmark(result)

    return result
  },

  async editBookmark({ commit, dispatch, state }, bookmark) {
    const result = await DatabaseService.editBookmark(bookmark)

    if (bookmark.dataImage) {
      await dispatch('addThumbnail', { id: bookmark.id, url: bookmark.url, image: bookmark.dataImage })
    }

    dispatch('loadBookmarks')
    SyncService.editBookmark(result)

    return result
  },

  async destroyBookmark({ commit, dispatch, state }, bookmark) {
    await DatabaseService.destroyBookmark(bookmark)
    SyncService.destroyBookmark(bookmark)
    dispatch('loadBookmarks')
  },

  async bulkDestroyBookmarks({ commit, dispatch, state }, ids) {
    await DatabaseService.bulkDestroyBookmarks(ids)
    SyncService.bulkDestroyBookmarks(ids)
    dispatch('loadBookmarks')
  },

  async bulkMoveBookmarks({ commit, dispatch, state }, { ids, groupId }) {
    await DatabaseService.bulkMoveBookmarks(ids, groupId)
    SyncService.bulkMoveBookmarks(ids, groupId)
    dispatch('loadBookmarks')
  },

  async createGroup({ commit, dispatch, state }, group) {
    const result = await DatabaseService.createGroup(group)
    SyncService.addGroup(result)

    dispatch('loadGroups')

    return result
  },

  async editGroup({ commit, dispatch, state }, group) {
    if (group.id == 0) {
      dispatch('preferences/updateDefaultGroupName', group.title, { root: true })
      dispatch('loadGroups')
      return group
    }

    const result = await DatabaseService.editGroup(group)
    SyncService.editGroup(result)

    dispatch('loadGroups')

    return result
  },

  async destroyGroup({ commit, dispatch, state }, group) {
    await DatabaseService.destroyGroup(group)
    SyncService.destroyGroup(group)
    dispatch('loadGroups')
    dispatch('cleanUpThumbnails')
  },

  async updateBookmarksPosition({ commit, dispatch, state }, data) {
    await DatabaseService.updateBookmarksPosition(data)
    SyncService.reorderBookmarks(data)
    dispatch('loadBookmarks')
  },

  async updateGroupsPosition({ commit, dispatch, state }, data) {
    await DatabaseService.updateGroupsPosition(data)
    SyncService.reorderGroups(data)
  },

  async addThumbnail({ commit, dispatch, state }, { id, url, image }) {
    await DatabaseService.addThumbnail({ id, url, image })
  },

  async getThumbnail({ commit, dispatch, state }, bookmark) {
    return await DatabaseService.getThumbnail(bookmark)
  },

  async cleanUpThumbnails() {
    await DatabaseService.cleanUpThumbnails()
  },

  async getBookmarksWithNoThumbnail({ commit, dispatch, state }) {
    const bookmarks = await DatabaseService.getBookmarksWithNoThumbnail()
    commit(types.SET_BOOKMARKS_WITH_NO_THUMBNAIL, bookmarks)
  },

  async getBookmarksWithThumbnail({ commit, dispatch, state }) {
    const bookmarks = await DatabaseService.getBookmarksWithThumbnail()
    commit(types.SET_BOOKMARKS_WITH_THUMBNAIL, bookmarks)
  },

  async getDuplicateBookmarksCount({ commit, dispatch, state }) {
    const ids = await DatabaseService.getDuplicateBookmarks()
    return ids.length
  },

  async removeDuplicateBookmarks({ commit, dispatch, state }) {
    const duplicateBookmarksIds = await DatabaseService.getDuplicateBookmarks()
    await dispatch('bulkDestroyBookmarks', duplicateBookmarksIds)
    const emptyGroups = await DatabaseService.getEmptyGroups()
    for (const group of emptyGroups) {
      await dispatch('destroyGroup', group)
    }
  },

  addBookmarkVisit({ commit, dispatch, state }, bookmarkId) {
    DatabaseService.trackVisit(bookmarkId)
  },

  initializeDiscount({ commit, state }) {
    const isElligible = !state.profile?.active && state.bookmarksCount > 25
    const discountDismissed = StorageService.load('discount-07-2022', false)

    if (isElligible && !discountDismissed) {
      commit(types.SET_SHOW_DISCOUNT, true)
    }
  }
}

const mutations = {
  [types.SET_BOOKMARKS](state, bookmarks) {
    state.bookmarks = bookmarks
  },

  [types.SET_GROUPS](state, groups) {
    state.groups = groups
  },

  [types.SET_SELECTED_GROUP](state, group) {
    state.selectedGroup = group
  },

  [types.SEARCH_QUERY_SET](state, obj) {
    state.query = obj
  },

  [types.SEARCH_RESULTS_SET](state, obj) {
    state.searchResults = obj
  },

  [types.SET_APP_LOADING](state, isLoading) {
    state.isLoading = isLoading
  },

  [types.SET_BOOKMARKS_COUNT](state, bookmarksCount) {
    state.bookmarksCount = bookmarksCount
  },

  [types.SET_BOOKMARKS_WITH_NO_THUMBNAIL](state, bookmarks) {
    state.bookmarksWithNoThumbnail = bookmarks
  },

  [types.SET_BOOKMARKS_WITH_THUMBNAIL](state, bookmarks) {
    state.bookmarksWithThumbnail = bookmarks
  },

  [types.SET_PROFILE](state, profile) {
    state.profile = profile
  },

  [types.SET_FREEMIUM](state, value) {
    state.freemium = value
  },

  [types.SET_SHOW_DISCOUNT](state, value) {
    state.showDiscount = value
  }
}

const getters = {
  bookmarks(state) {
    return state.bookmarks
  },

  groups(state) {
    return state.groups
  },

  visibleGroups(state, getters, rootState) {
    return state.groups.filter(({ id }) => !rootState.preferences.hiddenGroups.includes(id))
  },

  selectedGroup(state) {
    return state.selectedGroup
  },

  query(state) {
    return state.query
  },

  searchResults(state) {
    return state.searchResults
  },

  folder(state) {
    return state.currentFolder
  },

  isLoading(state) {
    return state.isLoading
  },

  isFreemium(state, getters) {
    return state.freemium && !getters['isPremium']
  },

  isPremium(state) {
    return state.profile?.active
  },

  isLegacyPlan(state) {
    return state.profile?.legacy
  },

  bookmarksCount(state) {
    return state.bookmarksCount
  },

  bookmarksWithNoThumbnail(state) {
    return state.bookmarksWithNoThumbnail
  },

  bookmarksWithThumbnail(state) {
    return state.bookmarksWithThumbnail
  },

  showDiscount(state) {
    return state.showDiscount
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
