<template>
  <sl-dialog
    no-header
    style="--header-spacing: 0; --body-spacing: 0; --footer-spacing: 0; --width: 48rem"
    @sl-after-hide="afterHide"
    @sl-after-show="afterShow"
    @sl-initial-focus="onFocus"
  >
    <div v-if="form" class="Modal">
      <div class="Modal__header">
        <h1 class="h1">{{ title }}</h1>
        <button class="Modal__close" name="button" type="button" @click="hide()">
          <sl-icon library="feather" name="x"></sl-icon>
        </button>
      </div>

      <div class="Modal__content">
        <div v-if="!isPopularWebsiteSelected && !isBulkMode" class="flex gap-4 items-center">
          <!-- Name -->
          <div class="w-3/5 Form__group">
            <label class="Form__label">
              <div class="flex justify-between">
                <div>{{ $i18n('url') }}</div>
                <div
                  v-if="!isEditing"
                  class="font-medium text-gray-400 hover:text-primary normal-case cursor-pointer"
                  @click="isBulkMode = !isBulkMode"
                >
                  Add multiple URLs
                </div>
              </div>
            </label>
            <div>
              <sl-input
                id="url"
                ref="url"
                inputmode="url"
                :placeholder="$i18n('newtab_type_url')"
                type="url"
                :value="form.url"
                @blur="onUrlBlur"
                @input="form.url = $event.target.value"
                @keydown.esc="onEscapeKey($event, 'url')"
              ></sl-input>
              <InputErrorMessage class="mt-2" :error="errors.url" />
            </div>
          </div>
          <!-- Description -->
          <div class="w-2/5 Form__group">
            <label class="Form__label">
              {{ $i18n('title') }}
            </label>
            <div>
              <sl-input
                :placeholder="$i18n('newtab_enter_name')"
                type="text"
                :value="form.title"
                @input="form.title = $event.target.value"
                @keydown.enter="submit"
                @keydown.esc="onEscapeKey($event, 'title')"
              ></sl-input>
              <InputErrorMessage class="mt-2" :error="errors.title" />
            </div>
          </div>
        </div>

        <div v-if="!isPopularWebsiteSelected && showPopularWebsites" class="flex gap-3 items-center mt-2 text-xs">
          <div class="w-full h-px border-t"></div>
          <div>{{ $i18n('or') }}</div>
          <div class="w-full h-px border-t"></div>
        </div>

        <!-- Most popular websites -->
        <div v-if="showPopularWebsites">
          <div class="Form__group">
            <div class="sticky -top-8 z-20" :class="{ '-mt-8': isPopularWebsiteSelected }">
              <div class="flex gap-4 justify-between items-center py-4 bg-white">
                <div class="w-3/5 Form__label" style="margin-bottom: 0">
                  {{ $i18n('newtab_thumbnail_popular') }}
                </div>
                <sl-input
                  v-if="!form?.url"
                  class="w-1/4"
                  filled
                  inputmode="search"
                  pill
                  :placeholder="$i18n('search')"
                  size="small"
                  type="search"
                  :value="query"
                  @input="query = $event.target.value"
                  @keydown.esc.stop="query = ''"
                >
                  <sl-icon slot="prefix" library="feather" name="search"></sl-icon>
                </sl-input>
              </div>
            </div>

            <div class="grid grid-cols-4 gap-1.5">
              <div
                v-for="item of popularWebsitesResult"
                :key="item.url"
                class="p-3 rounded border-2 cursor-pointer"
                :class="{ 'border-primary': item.selected }"
                @click="toggleWebsite(item)"
              >
                <div class="aspect-w-16 aspect-h-9">
                  <img class="object-contain w-100 h-100" loading="lazy" :src="item.logo" />
                </div>
                <div class="text-xs text-center">{{ item.title }}</div>
              </div>
            </div>
          </div>
        </div>

        <div v-if="form.title && form.url" class="Form__group">
          <label class="Form__label">
            {{ $i18n('newtab_customize_thumbnail') }}
          </label>
          <div class="flex mb-4 space-x-4">
            <div
              v-if="form.logo"
              class="flex justify-center items-center p-3 space-x-4 w-full hover:bg-gray-50 rounded border-2 cursor-pointer text-muted"
              :class="{ 'border-black': form.thumbnailSource === 'logo' }"
              @click="setThumbnailSource('logo')"
            >
              <img class="object-contain overflow-hidden w-16 h-16 rounded mix-blend-multiply" :src="form.logo" />
            </div>
            <div
              class="flex flex-col justify-center items-center p-3 space-y-2 w-full hover:bg-gray-50 rounded border-2 cursor-pointer"
              :class="{ 'border-black': form.thumbnailSource === 'capture' }"
              @click="setThumbnailSource('capture')"
            >
              <sl-icon class="text-2xl" name="window-fullscreen"></sl-icon>
              <div class="text-sm">Page capture</div>
            </div>
            <div
              class="flex relative flex-col justify-center items-center p-3 space-y-2 w-full hover:bg-gray-50 rounded border-2 cursor-pointer"
              :class="{
                'cursor-not-allowed opacity-75': !canSetCustomURL,
                'border-black': form.thumbnailSource === 'url'
              }"
              @click="canSetCustomURL && setThumbnailSource('url')"
            >
              <div
                v-if="!canSetCustomURL"
                class="absolute top-0 right-0 z-10 p-1 px-2 -mt-2 mr-2 text-xs font-bold text-white bg-red-400 hover:bg-red-500 rounded cursor-pointer"
                @click="$emit('upgrade')"
              >
                PRO
              </div>

              <sl-icon class="text-2xl" name="link-45deg"></sl-icon>
              <div class="text-sm">Custom URL</div>
            </div>
            <div
              class="flex relative flex-col justify-center items-center p-3 space-y-2 w-full hover:bg-gray-50 rounded border-2 cursor-pointer"
              :class="{
                'cursor-not-allowed opacity-75': !isPremium,
                'border-black': form.thumbnailSource === 'upload'
              }"
              @click="uploadImage()"
            >
              <div
                v-if="!isPremium"
                class="absolute top-0 right-0 z-10 p-1 px-2 -mt-2 mr-2 text-xs font-bold text-white bg-red-400 hover:bg-red-500 rounded cursor-pointer"
                @click="$emit('upgrade')"
              >
                PRO
              </div>
              <div v-if="uploadResult">
                <img class="object-contain overflow-hidden w-16 h-16 rounded mix-blend-multiply" :src="uploadResult" />
              </div>
              <div v-else class="flex flex-col justify-center items-center space-y-2">
                <sl-icon class="text-2xl" name="cloud-arrow-up"></sl-icon>
                <div class="text-sm">
                  {{ isUploading ? 'Uploading...' : 'Upload image' }}
                </div>
              </div>
            </div>
          </div>

          <div v-if="form.thumbnailSource === 'url'">
            <sl-input
              inputmode="url"
              :placeholder="$i18n('newtab_thumbnail_image_url')"
              type="url"
              :value="form.thumbnail"
              @input="form.thumbnail = $event.target.value"
            ></sl-input>
            <InputErrorMessage class="mt-2" :error="errors.thumbnail" />
          </div>

          <div v-if="form.thumbnailSource === 'upload'">
            <InputErrorMessage class="mt-2" :error="errors.upload" />
          </div>
        </div>

        <div v-if="isBulkMode" class="Form__group">
          <label class="Form__label">Import URLs</label>
          <div>
            <sl-textarea
              placeholder="Enter URLs separated by a new line"
              :value="bulkForm.urls"
              @input="bulkForm.urls = $event.target.value"
            ></sl-textarea>
            <InputErrorMessage class="mt-2" :error="errors.bulkForm" />
          </div>

          <div class="mt-2">
            <sl-checkbox
              :checked="bulkForm.captureWebsite"
              @sl-change="bulkForm.captureWebsite = !bulkForm.captureWebsite"
            >
              Capture screenshots (leave unchecked to use website logos)
            </sl-checkbox>
          </div>
        </div>
      </div>

      <div class="Modal__footer">
        <div class="flex justify-end space-x-2">
          <sl-button @click="hide()">
            {{ $i18n('cancel') }}
          </sl-button>
          <div v-if="isBulkMode">
            <sl-button :disabled="!bulkForm?.urls || isSubmittingBulk" variant="primary" @click="submitBulk()">
              {{ isSubmittingBulk ? 'Saving...' : $i18n('save') }}
            </sl-button>
          </div>
          <div v-else>
            <sl-button
              :disabled="isCapturingThumbnail || (!form.url && !isPopularWebsiteSelected)"
              variant="primary"
              @click="isPopularWebsiteSelected ? submitFromPopular() : submit()"
            >
              {{ isCapturingThumbnail ? 'Creating thumbnail...' : $i18n('save') }}
            </sl-button>
          </div>
        </div>
      </div>
    </div>
  </sl-dialog>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep'

import popularWebsites from '@/constants/popularWebsites'
import UploadService from '@/services/UploadService'
import ValidationService, { isRequired, isValidURL } from '@/services/ValidationService'
import { isFirefox } from '@/utils/browser'
import { fixUrl, getHostname, suggestTitleFromUrl } from '@/utils/url'

export default {
  props: {
    bookmark: {
      type: Object,
      required: false,
      default: () => {}
    },
    isFreemium: {
      type: Boolean,
      required: false,
      default: false
    },
    isPremium: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  emits: ['submit', 'submitMultiple'],

  data() {
    return {
      form: {},
      bulkForm: {},
      errors: {},
      websites: cloneDeep(popularWebsites),
      query: '',
      isCapturingThumbnail: false,
      isUploading: false,
      uploadResult: null,
      isBulkMode: false,
      isSubmittingBulk: false
    }
  },

  computed: {
    isEditing() {
      return this.form?.id
    },

    title() {
      return this.isEditing ? this.$i18n('newtab_edit_dial') : this.$i18n('newtab_add_new_dial')
    },

    showPopularWebsites() {
      if (this.isBulkMode) {
        return false
      }

      if (this.isEditing || this.form?.title) {
        return false
      }

      if (this.form?.url && this.popularWebsitesResult.length == 0) {
        return false
      }

      return true
    },

    isPopularWebsiteSelected() {
      return [...this.websites].filter(({ selected }) => selected).length > 0
    },

    popularWebsitesResult() {
      return [...this.websites].filter((website) => {
        let query = ''

        if (this.query && this.query.length > 0) {
          query = this.query
        } else if (this.form?.url) {
          query = this.form?.url
        }

        return (
          website.title.toLowerCase().indexOf(query.toLowerCase()) > -1 || website.url.indexOf(query.toLowerCase()) > -1
        )
      })
    },

    canSetCustomURL() {
      return !this.isFreemium
    }
  },

  methods: {
    show() {
      this.reset()
      this.init()
      this.$el.show()
    },

    hide() {
      document.activeElement?.blur()
      this.$el.hide()
    },

    afterShow() {
      document.getElementById('url')?.focus()
    },

    afterHide() {
      this.reset()
    },

    onFocus(e) {
      e.preventDefault()
      document.getElementById('url')?.focus({ preventScroll: true })
    },

    init() {
      this.form = { ...this.bookmark }

      if (this.isEditing) {
        this.suggestBookmarkLogo()
      }
    },

    reset() {
      this.form = {}
      this.bulkForm = {}
      this.isBulkMode = false
      this.isSubmittingBulk = false
      this.errors = {}
      this.websites = cloneDeep(popularWebsites)
      this.query = ''
      this.isCapturingThumbnail = false
      this.isUploading = false
      this.uploadResult = null
    },

    suggestBookmarkTitle: function () {
      if (!this.form.title || !this.form.id) {
        this.form.title = suggestTitleFromUrl(this.form.url)
        this.form = Object.assign({}, this.form)
      }
    },

    suggestBookmarkLogo() {
      if (!this.form.url) {
        return
      }

      const logo = new Image()
      const hostname = getHostname(fixUrl(this.form.url))

      const popularWebsite = popularWebsites.find(
        ({ url, alternativeUrls }) => url.indexOf(hostname) > -1 || (alternativeUrls || '').indexOf(hostname) > -1
      )
      const logoSrc = popularWebsite ? popularWebsite.logo : 'https://logo.clearbit.com/' + hostname + '?size=320'

      if (!hostname) return

      if (!this.form.thumbnailSource) {
        if (this.form.thumbnail) {
          this.form.thumbnailSource = 'url'
        } else {
          this.form.thumbnailSource = 'capture'
        }
      }

      // Fetch logo
      logo.addEventListener('load', () => {
        // New bookmark
        if (!this.form.id) {
          this.form.thumbnailSource = 'logo'
        }
        // Existing bookmark
        if (this.form.thumbnail == logoSrc) {
          this.form.thumbnailSource = 'logo'
        }
        this.form.logo = logoSrc

        this.form = { ...this.form }
      })

      logo.addEventListener('error', () => {
        this.form.logo = undefined
        if (this.form.thumbnailSource === 'logo') {
          this.form.thumbnailSource = 'capture'
        }
      })

      logo.src = logoSrc
    },

    onUrlBlur: function () {
      if (!this.form.url) {
        return
      }

      this.$nextTick(() => {
        setTimeout(() => {
          this.form.url = fixUrl(this.form.url)
          this.suggestBookmarkTitle()
          this.suggestBookmarkLogo()
        }, 160)
      })
    },

    onEscapeKey: function (e, attr) {
      if (!this.isEditing && this.form[attr]) {
        this.form[attr] = ''
        e.preventDefault()
        e.stopPropagation()
      }
    },

    setThumbnailSource(source) {
      this.form.thumbnailSource = source
      this.form = Object.assign({}, this.form)
    },

    async toggleWebsite(item) {
      if (this.form?.url && !this.query) {
        this.form = {
          ...this.form,
          url: item.url,
          title: item.title,
          logo: item.logo,
          thumbnailSource: 'logo'
        }
        this.submit()
      } else {
        item.selected = !item.selected
        this.websites = [...this.websites]
      }
    },

    handleFileUpload(file) {
      if (!file) {
        return
      }

      this.errors.upload = null

      if (file.size >= 2000 * 1024) {
        this.errors.upload = 'Selected file too large. Maximum upload size is 2 MB.'
        return
      }

      this.isUploading = true
      this.uploadResult = null

      UploadService.upload(file)
        .then((response) => {
          if (response?.url) {
            this.uploadResult = response?.url
          }
        })
        .catch((e) => {
          this.errors.upload = 'Error while uploading the file'
        })
        .finally(() => {
          this.isUploading = false
        })
    },

    uploadImage() {
      if (!this.isPremium) {
        return
      }

      let uploader
      this.errors.upload = null
      this.setThumbnailSource('upload')

      uploader = document.createElement('input')
      uploader.type = 'file'
      uploader.accept = 'image/png, image/jpeg, image/gif'

      uploader.addEventListener('change', (e) => {
        this.handleFileUpload(e.target.files[0])
      })

      uploader.click()
    },

    async validate() {
      this.errors = {}

      const rules = {
        title: [isRequired('Please enter the title')],
        url: [isRequired(), isValidURL('Please enter correct URL')],
        thumbnail: [isValidURL()]
      }

      const result = await ValidationService.validate(this.form, rules)

      if (!result.isValid) {
        this.result = undefined
        this.errors = result.errors
        return false
      }

      return true
    },

    async submitBulk() {
      this.errors.bulkForm = null
      const urls = (this.bulkForm?.urls || '')
        .split('\n')
        .map((url) => url.trim())
        .filter((url) => url)

      const bookmarks = []

      for (const url of urls) {
        try {
          new URL(url)

          let thumbnail = null

          if (!this.bulkForm.captureWebsite) {
            const hostname = getHostname(fixUrl(url))
            const popularWebsite = popularWebsites.find(
              ({ url, alternativeUrls }) => url.indexOf(hostname) > -1 || (alternativeUrls || '').indexOf(hostname) > -1
            )

            thumbnail = popularWebsite ? popularWebsite.logo : 'https://logo.clearbit.com/' + hostname + '?size=320'
          }

          bookmarks.push({
            url,
            title: suggestTitleFromUrl(url),
            thumbnail,
            idgroup: this.form.idgroup
          })
        } catch (e) {
          // noop
        }
      }

      if (bookmarks.length === 0) {
        this.errors.bulkForm = 'Please enter at least one valid URL'
        return
      }

      this.isSubmittingBulk = true
      this.$emit('submitMultiple', bookmarks)
    },

    async submit() {
      if (!(await this.validate())) {
        return
      }

      if (this.form.thumbnailSource === 'logo') {
        this.form.thumbnail = this.form.logo
      }

      if (this.form.thumbnailSource === 'upload' && this.uploadResult) {
        this.form.thumbnail = this.uploadResult
      }

      if (this.form.thumbnailSource === 'capture') {
        this.form.thumbnail = ''

        if (isFirefox()) {
          try {
            await browser.permissions.request({ origins: ['<all_urls>'] })
          } catch {
            // no permission
          }
        }
      }

      this.$emit('submit', this.form)

      // Delayed bookmark thumbanil capture
      if (this.form.thumbnailSource === 'capture' && !this.form.dataThumbnail) {
        this.isCapturingThumbnail = true
      } else {
        this.hide()
      }
    },

    async submitFromPopular() {
      const bookmarks = this.websites
        .filter(({ selected }) => selected)
        .map((website) => {
          return {
            url: website.url,
            title: website.title,
            thumbnail: website.logo,
            idgroup: this.form.idgroup
          }
        })

      this.$emit('submitMultiple', bookmarks)
      this.hide()
    }
  }
}
</script>

<style lang="postcss" scoped>
.Modal {
  max-height: 586px;

  @media (min-height: 1024px) {
    max-height: 57vh;
  }
}
</style>
