<template>
  <div class="absolute inset-x-0 top-0 z-10">
    <div class="SimpleHeader" :style="compactHeaderStyle">
      <!-- Group Toggle Button -->
      <div
        v-if="hasGroups"
        class="group relative SimpleHeader__toggle"
        :class="{ 'SimpleHeader__toggle--active': isGroupSelectorOpen }"
        :style="textStyle"
        @click="toggleGroupSelector"
      >
        <div class="relative z-10">{{ selectedGroupDetails?.title }}</div>
        <sl-icon
          class="ml-1.5 text-xl transform-gpu duration-0"
          :class="{ 'rotate-180 duration-0': isGroupSelectorOpen }"
          library="feather"
          name="chevron-down"
          :style="iconStyle"
        ></sl-icon>
      </div>

      <!-- Group Selector Dropdown -->
      <div v-if="hasGroups && isGroupSelectorOpen" class="GroupSelector">
        <div class="GroupSelector__Backdrop" @click="closeGroupSelector"></div>
        <div class="GroupSelector__Content">
          <div class="flex justify-between items-center px-4 mb-2">
            <h2 class="text-lg font-bold">
              <span>{{ $i18n('groups') }}</span>
            </h2>
            <div
              class="p-2 hover:underline hover:bg-neutral-100 rounded-full cursor-pointer"
              @click="closeGroupSelector"
            >
              <sl-icon library="feather" name="x"></sl-icon>
            </div>
          </div>
          <div class="flex overflow-y-auto flex-col">
            <draggable
              ref="groups"
              v-model="groups"
              :animation="100"
              :class="{ 'Groups--dragging': drag }"
              :delay="100"
              :delay-on-touch-only="true"
              :disabled="!canReorderGroups"
              handle=".drag-handle"
              item-key="id"
              @change="handleReorder"
              @end="endDrag"
              @start="startDrag"
            >
              <template #item="{ element }">
                <router-link
                  class="group flex gap-1 justify-between items-center py-2 px-4 w-full hover:bg-gray-100 rounded cursor-pointer select-none"
                  :class="{ 'font-bold': selectedGroup == element.id }"
                  :to="{ name: 'home', params: { id: element.id } }"
                  @click="closeGroupSelector"
                >
                  {{ element.title }}
                  <div
                    v-if="selectedGroup == element.id"
                    class="hidden group-hover:block p-1 px-3 ml-auto text-xs font-medium bg-gray-200 hover:bg-gray-300 rounded-lg cursor-pointer"
                    @click.stop="editGroup(element)"
                  >
                    Edit
                  </div>
                  <svg
                    class="hidden group-hover:block w-8 cursor-move drag-handle"
                    height="18"
                    style="fill: currentColor"
                    version="1.1"
                    viewBox="0 0 1024 1024"
                    width="18"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M219.428571 585.142857c-36.571429 0-73.142857 36.571429-73.142857 73.142857s36.571429 73.142857 73.142857 73.142857 73.142857-36.571429 73.142858-73.142857-36.571429-73.142857-73.142858-73.142857z m585.142858-146.285714c36.571429 0 73.142857-36.571429 73.142857-73.142857s-36.571429-73.142857-73.142857-73.142857-73.142857 36.571429-73.142858 73.142857 36.571429 73.142857 73.142858 73.142857zM219.428571 292.571429c-36.571429 0-73.142857 36.571429-73.142857 73.142857s36.571429 73.142857 73.142857 73.142857 73.142857-36.571429 73.142858-73.142857-36.571429-73.142857-73.142858-73.142857z m585.142858 292.571428c-36.571429 0-73.142857 36.571429-73.142858 73.142857s36.571429 73.142857 73.142858 73.142857 73.142857-36.571429 73.142857-73.142857-36.571429-73.142857-73.142857-73.142857zM512 292.571429c-36.571429 0-73.142857 36.571429-73.142857 73.142857s36.571429 73.142857 73.142857 73.142857 73.142857-36.571429 73.142857-73.142857-36.571429-73.142857-73.142857-73.142857z m0 292.571428c-36.571429 0-73.142857 36.571429-73.142857 73.142857s36.571429 73.142857 73.142857 73.142857 73.142857-36.571429 73.142857-73.142857-36.571429-73.142857-73.142857-73.142857z"
                    />
                  </svg>
                </router-link>
              </template>
            </draggable>
            <hr class="m-2 border-t border-gray-100" />
            <button
              class="group flex gap-1 justify-between items-center py-2 px-4 w-full hover:bg-gray-100 rounded cursor-pointer select-none"
              @click="createGroup"
            >
              <span>{{ $i18n('newtab_create_group') }}</span>
              <sl-icon class="text-base" library="feather" name="plus"></sl-icon>
            </button>
          </div>
        </div>
      </div>

      <!-- Search Bar -->
      <div v-if="hasGroups" class="Search">
        <sl-icon class="Search__Icon" library="feather" name="search"></sl-icon>
        <vue3-simple-typeahead
          ref="searchInput"
          class="Search__input"
          :item-projection="itemProjection"
          :items="groups"
          :min-input-length="1"
          :placeholder="$i18n('search')"
          :select-on-tab="false"
          @keydown.esc.stop="clearSearch"
          @keydown.tab.stop="onTabKeyEventHandler"
          @onInput="handleInput"
          @selectItem="onSelectItemEventHandler"
        ></vue3-simple-typeahead>
      </div>

      <!-- Settings Button -->
      <div class="HeaderButton" @click="$emit('openSettings')">
        <sl-icon
          class="text-base opacity-75 hover:opacity-100"
          library="feather"
          name="menu"
          :style="iconStyle"
        ></sl-icon>
      </div>
    </div>
  </div>
</template>

<script>
import { create } from 'lodash'
import draggable from 'vuedraggable'
import { mapGetters } from 'vuex'

export default {
  components: {
    draggable
  },
  props: {
    groups: {
      type: Array,
      default: () => []
    },
    selectedGroup: {
      type: String,
      default: '0'
    },
    preferences: {
      type: Object,
      required: true
    },
    query: {
      type: String,
      default: ''
    }
  },
  emits: ['update:query', 'onReorder', 'onCreateGroup', 'onFocusBookmarks', 'onEditGroup'],
  data() {
    return {
      drag: false,
      hiddenGroups: [],
      isGroupSelectorOpen: false,
      dragOptions: {
        disabled: false,
        animation: 100,
        delay: 100,
        handle: '.drag-handle',
        delayOnTouchOnly: true
      }
    }
  },
  computed: {
    ...mapGetters('theme', ['compactHeaderStyle', 'textStyle', 'activeItemStyle', 'iconStyle']),
    canReorderGroups() {
      return this.preferences?.orderGroupsBy == 'manual'
    },
    hasGroups() {
      return this.groups.length > 1
    },
    hasHiddenGroups() {
      return this.hiddenGroups.length > 0
    },
    selectedGroupDetails() {
      return this.groups.find((group) => group.id == this.selectedGroup)
    }
  },
  methods: {
    toggleGroupSelector() {
      this.isGroupSelectorOpen = !this.isGroupSelectorOpen
    },
    createGroup() {
      this.$emit('onCreateGroup')
      this.closeGroupSelector()
    },
    closeGroupSelector() {
      this.isGroupSelectorOpen = false
    },
    clearSearch() {
      this.$refs.searchInput.clearInput()
      this.$emit('update:query', null)
    },
    editGroup(group) {
      this.$emit('onEditGroup', group)
    },
    handleInput({ input }) {
      this.$emit('update:query', input)
    },
    onSelectItemEventHandler(item) {
      this.clearSearch()
      this.$router.push({ name: 'home', params: { id: item.id } })
      setTimeout(() => {
        this.$emit('onFocusBookmarks')
      }, 100)
    },
    onTabKeyEventHandler() {
      this.$emit('onFocusBookmarks')
    },
    handleReorder() {
      this.$nextTick(() => {
        const sorted = this.groups.map((group, index) => ({
          id: group.id,
          index
        }))
        this.$emit('onReorder', sorted)
      })
    },
    startDrag() {
      this.drag = true
    },
    endDrag() {
      this.drag = false
    },
    itemProjection(group) {
      return group.title
    }
  }
}
</script>

