<template>
  <div
    style="border-radius: 50%; overflow: hidden; border: 1px solid rgba(0, 0, 0, 0.15)"
    :style="{ width: `${size}px`, height: `${size}px` }"
  >
    <img :alt="alt" :src="url" />
  </div>
</template>

<script>
import md5 from 'blueimp-md5'

export default {
  name: 'GravatarImg',

  inheritAttrs: false,

  props: {
    email: {
      type: String,
      default: ''
    },

    hash: {
      type: String,
      default: ''
    },

    size: {
      type: Number,
      default: 80
    },

    defaultImg: {
      type: String,
      default: 'mm'
    },

    rating: {
      type: String,
      default: 'g'
    },

    alt: {
      type: String,
      default: 'Avatar'
    },

    protocol: {
      type: String,
      default: 'https'
    },

    hostname: {
      type: String,
      default: 'www.gravatar.com'
    }
  },

  computed: {
    url() {
      const protocol = this.protocol.slice(-1) === ':' ? this.protocol : `${this.protocol}:`

      const img = [
        `${protocol === ':' ? '' : protocol}//${this.hostname}/avatar/`,
        this.hash || md5(this.email.trim().toLowerCase()),
        `?s=${this.size * 2}`,
        `&d=${this.defaultImg}`,
        `&r=${this.rating}`
      ]

      return img.join('')
    }
  }
}
</script>
