<template>
  <div class="SidebarPanel">
    <div class="flex gap-12 justify-between SidebarPanel__header SidebarPanel__header--no-border">
      <div>{{ $i18n('bookmarks') }}</div>

      <sl-dropdown>
        <sl-button slot="trigger" outline size="small" variant="default">
          <sl-icon class="mr-1 w-4 h-4" name="sort-down"></sl-icon>
          {{ preferences.sidebarBookmarksOrder == 'manual' ? 'Manual' : 'Auto' }}
        </sl-button>

        <sl-menu>
          <sl-menu-item size="small" @click="updateOrder('manual')">Manual order</sl-menu-item>
          <sl-menu-item size="small" @click="updateOrder('auto')">Automatic order</sl-menu-item>
        </sl-menu>
      </sl-dropdown>
    </div>

    <div class="SidebarPanel__nav">
      <sl-input
        ref="search"
        inputmode="search"
        :placeholder="$i18n('search')"
        tabindex="0"
        type="search"
        :value="query"
        x-webkit-speech
        @input="searchBookmarks($event.target.value)"
        @keydown.esc.stop="query = ''"
      >
        <sl-icon slot="prefix" library="feather" name="search"></sl-icon>
      </sl-input>
    </div>

    <div v-if="!hasPermission" class="p-6 text-center SidebarPanel__body">
      <h3 class="text-base leading-relaxed text-muted">Speed Dial 2 needs your permission to display bookmarks.</h3>
      <sl-button class="mt-4" @click="requestPermissions()">Allow access to bookmarks</sl-button>
    </div>

    <div v-else class="SidebarPanel__body" :class="{ 'opacity-75': isLoading }">
      <div v-if="query && !searchResults.length" class="p-2">No results.</div>

      <div v-if="folder" class="px-1 pb-3 my-1 mb-3 border-b">
        <sl-breadcrumb style="--sl-spacing-x-small: 4px; --sl-color-primary-600: black">
          <sl-breadcrumb-item @click="setFolder(undefined)">All</sl-breadcrumb-item>
          <sl-breadcrumb-item v-for="node in folder.parents" :key="node.id" @click="setFolder(node.id)">
            {{ node.title }}
          </sl-breadcrumb-item>
          <sl-breadcrumb-item>{{ folder.title }}</sl-breadcrumb-item>
        </sl-breadcrumb>
      </div>

      <div class="w-full">
        <BrowserBookmarksList
          :bookmarks="computedBookmarks"
          :folder="folder"
          @folderSelected="setFolder"
          @navigateToSearch="focusSearch"
          @reset="initialize"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import BrowserBookmarksList from '@/components/BrowserBookmarksList.vue'

export default {
  components: {
    BrowserBookmarksList
  },

  props: {
    preferences: {
      type: Object,
      required: true
    }
  },

  emits: ['onUpdatePreferences'],

  data() {
    return {
      hasPermission: true
    }
  },

  computed: {
    ...mapGetters('browserBookmarks', ['bookmarks', 'folder', 'searchResults', 'isLoading', 'query']),

    computedBookmarks() {
      if (this.folder) {
        return this.folder.bookmarks
      } else if (this.query) {
        return this.searchResults
      } else {
        return this.bookmarks
      }
    }
  },

  beforeMount() {
    this.checkPermissions()
  },

  methods: {
    ...mapActions('browserBookmarks', ['init', 'setFolder', 'searchBookmarks']),

    initialize() {
      this.init()
      this.$nextTick(() => {
        this.$refs.search?.focus()
      })
    },

    updateOrder(order) {
      const preferences = {
        ...this.preferences,
        sidebarBookmarksOrder: order
      }

      this.$emit('onUpdatePreferences', { preferences })

      this.$nextTick(() => {
        this.initialize()
      })
    },

    focusSearch() {
      this.$refs.search?.focus()
    },

    requestPermissions() {
      browser.permissions
        .request({
          permissions: ['bookmarks']
        })
        .then((granted) => {
          if (granted) {
            this.hasPermission = granted
            this.initialize()
          }
        })
    },

    checkPermissions() {
      browser.permissions.contains({ permissions: ['bookmarks'] }).then((granted) => {
        this.hasPermission = granted

        if (granted) {
          this.initialize()
        }
      })
    }
  }
}
</script>
