<template>
  <sl-dialog
    no-header
    style="--header-spacing: 0; --body-spacing: 0; --footer-spacing: 0"
    :style="`--width: 56rem; --sl-overlay-background-color: rgba(30,30,50,0.25);`"
    @sl-after-hide="afterHide"
    @sl-after-show="afterShow"
    @sl-initial-focus="onFocus"
    @sl-request-close="(e) => e.preventDefault()"
  >
    <div class="Modal">
      <div class="Modal__content">
        <!-- Setup up Bookmarks -->
        <div v-if="section == 'bookmarks'" class="flex flex-col flex-1 py-8 px-6 pt-6">
          <div class="flex justify-between items-start">
            <div>
              <h2 class="mb-4 font-bold title-1">{{ $i18n('welcome_1_h1') }}</h2>
              <p class="lead">{{ $i18n('welcome_10_1') }}</p>
            </div>
            <button class="Modal__close" name="button" type="button" @click="hide()">
              <sl-icon library="feather" name="x"></sl-icon>
            </button>
          </div>

          <div class="grid grid-cols-4 gap-4 my-8">
            <div
              v-for="(item, index) of topSites"
              :key="index"
              class="relative p-2 rounded border-2 cursor-pointer"
              :class="{ 'border-primary': item.selected }"
              @click="item.selected = !item.selected"
            >
              <div class="absolute top-0 left-0 z-50 shrink-0 m-3 text-white pointer-events-none">
                <div
                  class="flex justify-center items-center w-5 h-5 rounded shadow"
                  :class="{
                    'bg-primary border-primary text-white': item.selected,
                    'bg-white border border-gray-400': !item.selected
                  }"
                >
                  <sl-icon library="feather" name="check"></sl-icon>
                </div>
              </div>

              <div class="my-2 aspect-w-16 aspect-h-9">
                <img class="object-contain w-100 h-100" :src="item.thumbnail" />
              </div>
              <div class="text-xs text-center truncate">{{ item.title }}</div>
            </div>
          </div>

          <div class="flex flex-1 justify-between items-center pt-6 -mb-6 border-t">
            <div>
              {{ $i18n('already_have_an_account') }}
              <a class="font-semibold text-primary cursor-pointer" @click="showSignIn">{{ $i18n('sign_in') }}</a>
            </div>

            <div>
              <div class="flex items-center space-x-4">
                <sl-button size="large" @click="skip()">
                  <span class="capitalize">{{ $i18n('skip') }}</span>
                </sl-button>
                <span class="mx-8">or</span>
                <sl-button size="large" variant="primary" @click="submit()">
                  {{ $i18n('continue') }}
                  <sl-icon class="ml-1" library="feather" name="arrow-right"></sl-icon>
                </sl-button>
              </div>
            </div>
          </div>
        </div>

        <!-- Setup up Background -->
        <div v-if="section == 'background'" class="flex flex-col flex-1 py-8 px-6 pt-6">
          <div class="flex justify-between items-start">
            <div>
              <h2 class="mb-4 font-bold title-1">{{ $i18n('select_background') }}</h2>
            </div>
            <button class="Modal__close" name="button" type="button" @click="hide()">
              <sl-icon library="feather" name="x"></sl-icon>
            </button>
          </div>

          <div class="grid grid-cols-4 gap-4 my-6">
            <div
              v-for="(theme, index) in themes"
              :key="index"
              class="overflow-hidden text-center rounded border-2 opacity-90 hover:opacity-100 cursor-pointer aspect-video"
              :class="{ 'border-primary': preferences.theme.light.backgroundImage === theme.url }"
              :style="{ 'background-color': theme.color || '#fff', 'line-height': '6rem' }"
              @click="setTheme(theme)"
            >
              <img
                v-if="theme.preview || theme.url"
                class="object-cover w-full h-full"
                :src="theme.preview || theme.url"
              />
            </div>
          </div>

          <div class="mb-6 text-center">
            <a class="font-semibold text-primary cursor-pointer" @click="openPreferences">
              {{ $i18n('options_themes_more_themes') }}
            </a>
          </div>

          <div class="flex flex-1 justify-between items-center pt-6 -mb-6 border-t">
            <div>
              {{ $i18n('already_have_an_account') }}
              <a class="font-semibold text-primary cursor-pointer" @click="showSignIn">{{ $i18n('sign_in') }}</a>
            </div>

            <div>
              <div class="flex items-center space-x-4">
                <sl-button size="large" @click="setSection('bookmarks')">
                  <span class="capitalize">{{ $i18n('back') }}</span>
                </sl-button>
                <span class="mx-8">or</span>
                <sl-button size="large" variant="primary" @click="submit()">
                  {{ $i18n('continue') }}
                  <sl-icon class="ml-1" library="feather" name="arrow-right"></sl-icon>
                </sl-button>
              </div>
            </div>
          </div>
        </div>

        <div v-if="section == 'permissions'" class="flex flex-col flex-1 py-8 px-6 pt-6">
          <div class="flex overflow-hidden relative justify-between items-start">
            <div class="flex w-1/2">
              <div class="pr-8">
                <h2 class="mb-4 font-bold title-1">Website thumbnails</h2>
                <p class="mb-32 lead">
                  Speed Dial 2 needs additional permissions in order to automatically generate screenshots of your
                  favorite websites. Please click the button below to grant these permissions.
                </p>
              </div>
              <div class="absolute top-16 left-1/2">
                <img class="w-full" src="https://www.speeddial2.com/images/home/features-3.png" />
              </div>
            </div>
            <button class="Modal__close" name="button" type="button" @click="hide()">
              <sl-icon library="feather" name="x"></sl-icon>
            </button>
          </div>

          <div class="flex flex-1 justify-between items-center pt-6 -mb-6 border-t">
            <div>
              {{ $i18n('already_have_an_account') }}
              <a class="font-semibold text-primary cursor-pointer" @click="showSignIn">{{ $i18n('sign_in') }}</a>
            </div>

            <div>
              <div class="flex items-center space-x-4">
                <sl-button size="large" variant="primary" @click="grantPermissions()">
                  {{ $i18n('continue') }}
                  <sl-icon class="ml-1" library="feather" name="arrow-right"></sl-icon>
                </sl-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </sl-dialog>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep'
import { mapActions } from 'vuex'

import popularWebsites from '@/constants/popularWebsites'
import themes from '@/constants/themes'
import StorageService from '@/services/StorageService'
import { isFirefox } from '@/utils/browser'
import { fixUrl, getHostname, suggestTitleFromUrl, validateUrl } from '@/utils/url'

export default {
  props: {
    preferences: {
      type: Object,
      required: true
    }
  },

  emits: ['onSignIn', 'onUpdatePreferences', 'onOpenPreferences'],

  data() {
    return {
      themes: themes.filter(({ premium }) => !premium),
      topSites: [],
      section: 'bookmarks'
    }
  },

  methods: {
    ...mapActions('bookmarks', ['createBookmark']),

    async init() {
      this.sites = []
      const sites = await browser.topSites.get()
      // TODO: Set default theme
      this.setTheme(this.themes[7])

      sites
        .filter(({ url }) => validateUrl(url) && url.indexOf('localhost') == -1)
        .slice(0, 7)
        .forEach((item) => {
          const hostname = getHostname(fixUrl(item.url))

          // Try to find logo in popular websites
          const popularWebsite = popularWebsites.find(
            ({ url, alternativeUrls }) => url.indexOf(hostname) > -1 || (alternativeUrls || '').indexOf(hostname) > -1
          )
          const thumbnail = popularWebsite ? popularWebsite.logo : 'https://logo.clearbit.com/' + hostname + '?size=320'

          this.topSites.push({
            title: suggestTitleFromUrl(item.url),
            url: item.url,
            selected: true,
            thumbnail: thumbnail
          })
        })
    },

    show() {
      this.init()
      this.$el.show()
    },

    showSignIn() {
      this.$emit('onSignIn')
      this.hide()
    },

    openPreferences() {
      this.$emit('onOpenPreferences')
      this.hide()
    },

    setSection(section) {
      this.section = section
    },

    setTheme(theme) {
      const preferences = cloneDeep(this.preferences)
      preferences.theme.light = {
        ...preferences.theme.light,
        backgroundColor: theme.color,
        backgroundImage: theme.url,
        backgroundRepeat: theme.repeat,
        backgroundPosition: theme.position,
        backgroundSize: theme.size
      }
      preferences.theme.dark = {
        ...preferences.theme.dark,
        // backgroundColor: theme.color,
        backgroundImage: theme.url,
        backgroundRepeat: theme.repeat,
        backgroundPosition: theme.position,
        backgroundSize: theme.size
      }
      this.$emit('onUpdatePreferences', { preferences })
    },

    async submit() {
      if (this.section == 'bookmarks') {
        StorageService.persist('onboardingComplete', true)
        const selectedWebsites = this.topSites.filter(({ selected }) => selected)
        for (const item of selectedWebsites) {
          await this.createBookmark({ title: item.title, url: item.url, thumbnail: item.thumbnail })
        }
        this.setSection('background')
      } else if (this.section == 'background') {
        if (isFirefox()) {
          this.setSection('permissions')
        } else {
          this.hide()
        }
      }
    },

    async grantPermissions() {
      await browser.permissions.request({ origins: ['<all_urls>'] })
      this.hide()
    },

    hide() {
      document.activeElement?.blur()
      StorageService.persist('onboardingComplete', true)
      this.$el.hide()
    },

    skip() {
      if (this.section == 'bookmarks') {
        this.setSection('background')
      } else if (this.section == 'background') {
        this.hide()
      }
    },

    afterShow() {},

    afterHide() {},

    onFocus(e) {}
  }
}
</script>
