export const sortBookmarks = (bookmarks) => {
  return [...bookmarks]
    .sort((a, b) => {
      // Check if both are folders or both are bookmarks
      const aIsFolder = !!(a.children || !a.url)
      const bIsFolder = !!(b.children || !b.url)

      if (aIsFolder && bIsFolder) {
        // Both are folders, sort by title
        return a.title.localeCompare(b.title)
      } else if (!aIsFolder && !bIsFolder) {
        // Both are bookmarks, sort by title
        return a.title.localeCompare(b.title)
      } else {
        // One is folder and the other is not, folders come first
        return aIsFolder ? -1 : 1
      }
    })
    .filter(({ children }) => !children || (children && children.length > 0))
}

export const getParent = async (item, recursive = true) => {
  const parents = []

  if (recursive) {
    while (Number(item.parentId) > 0) {
      const result = await browser.bookmarks.get(item.parentId)
      item = result[0]
      parents.push(item)
    }
  } else {
    const result = await browser.bookmarks.get(item.parentId)
    item = result[0]
    parents.push(item)
  }

  return parents.reverse()
}

const getBookmarkParent = async (item) => {
  const parents = await getParent(item, false)
  return Promise.resolve({
    ...item,
    parents
  })
}

export const getParents = async (bookmarks) => {
  return Promise.all(
    bookmarks.map((item) => {
      return getBookmarkParent(item)
    })
  )
}
