import { isWindows } from '@/utils/os'
import resizeImage from '@/utils/resizeImage'

const EMPTY_IMAGE =
  'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII='

export default async function createPageScreenshot(url) {
  const base64 = await captureInBackground(url)

  if (base64 === EMPTY_IMAGE) {
    return EMPTY_IMAGE
  }

  const image = await resizeImage(base64)

  return image
}

const captureInBackground = (url) => {
  return new Promise((resolve, reject) => {
    browser.windows
      .create({
        url: url,
        focused: !1,
        width: isWindows() ? 100 : 1,
        height: isWindows() ? 100 : 1,
        left: 0,
        top: 0,
        type: 'popup'
      })
      .then((a) => {
        if (!a.tabs || !a.tabs.length) {
          browser.windows.remove(a.id)
          return reject(null)
        }

        const c = a.tabs[0].id
        let d

        browser.tabs.update(c, {
          muted: !0
        })
        browser.windows.update(a.id, {
          focused: !1,
          width: 1440,
          height: 900,
          left: 0,
          top: 0
        })

        const e = setTimeout(() => {
          clearInterval(d)
          browser.windows.remove(a.id)
          resolve(EMPTY_IMAGE)
        }, 10000)

        d = setInterval(() => {
          browser.tabs.get(c).then((c) => {
            'complete' === c.status &&
              (clearInterval(d),
              setTimeout(() => {
                clearTimeout(e)
                browser.tabs
                  .captureVisibleTab(a.id)
                  .then((c) => {
                    browser.windows.remove(a.id).then(() => {
                      c ? resolve(c) : resolve(EMPTY_IMAGE)
                    })
                  })
                  .catch(() => {
                    browser.windows.remove(a.id).then(() => {
                      resolve(EMPTY_IMAGE)
                    })
                  })
              }, 2500))
          })
        }, 200)
      })
  })
}
