<template>
  <div v-if="duplicateCount" class="pt-8 border-t">
    <div class="Form__group">
      <label class="Form__label">Duplicate bookmarks</label>
      <div class="text-sm">
        We found {{ duplicateCount }} duplicate bookmarks.
        <a class="text-primary cursor-pointer" @click="removeDuplicates()">Remove duplicates.</a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  data() {
    return {
      duplicateCount: 0
    }
  },

  async mounted() {
    this.duplicateCount = await this.getDuplicateBookmarksCount()
  },

  methods: {
    ...mapActions('bookmarks', ['getDuplicateBookmarksCount', 'removeDuplicateBookmarks']),

    async removeDuplicates() {
      await this.removeDuplicateBookmarks()
      this.duplicateCount = await this.getDuplicateBookmarksCount()
    }
  }
}
</script>
