                      >
                        Learn more
                      </a>
                    </p>
                  </div>
                </div>

                <!-- Upgrade to Pro -->
                <div v-else class="flex flex-col flex-1 h-full">
                  <!-- Upgrade to Pro -->
                  <div class="flex flex-col flex-1 px-2 mt-10">
                    <h2 class="title-2">{{ $i18n('pro_sync_upgrade') }}</h2>
                    <h2 v-if="hasDiscount" class="mt-2 title-3">
                      <span class="inline-block mr-2 text-red-500 line-through">$12</span>
                      $9
                      <span class="font-normal">/ your first year</span>
                    </h2>
                    <h2 v-else class="mt-2 title-3">
                      $12
                      <span class="font-normal">/ {{ $i18n('year') }}</span>
                    </h2>
                    <div class="flex flex-1">
                      <div class="w-7/12">
                        <ul class="my-6 text-base leading-relaxed">
                          <li class="flex items-start space-x-2">
                            <sl-icon class="shrink-0 mt-2 text-primary" library="feather" name="check"></sl-icon>
                            <div>{{ $i18n('pro_sync_features_0') }}</div>
                          </li>
                          <li class="flex items-start space-x-2">
                            <sl-icon class="shrink-0 mt-2 text-primary" library="feather" name="check"></sl-icon>
                            <div>{{ $i18n('pro_sync_features_1') }}</div>
                          </li>
                          <li class="flex items-start space-x-2">
                            <sl-icon class="shrink-0 mt-2 text-primary" library="feather" name="check"></sl-icon>
                            <div>{{ $i18n('pro_sync_features_2') }}</div>
                          </li>
                          <li class="flex items-start space-x-2">
                            <sl-icon class="shrink-0 mt-2 text-primary" library="feather" name="check"></sl-icon>
                            <div>{{ $i18n('pro_sync_features_3') }}</div>
                          </li>
                          <li class="flex items-start space-x-2">
                            <sl-icon class="shrink-0 mt-2 text-primary" library="feather" name="check"></sl-icon>
                            <div>{{ $i18n('pro_sync_features_4') }}</div>
                          </li>
                          <li class="flex items-start space-x-2">
                            <sl-icon class="shrink-0 mt-2 text-primary" library="feather" name="check"></sl-icon>
                            <div>{{ $i18n('share_to_web_helper') }}</div>
                          </li>
                        </ul>

                        <div v-if="signedIn">
                          <sl-button variant="primary" @click="checkout">Upgrade to Pro</sl-button>
                        </div>
                        <div v-else>
                          <sl-button variant="primary" @click="setAuthView('sign-up')">
                            {{ $i18n('sign_up') }}
                          </sl-button>
                        </div>
                      </div>
                      <div class="self-end w-5/12">
                        <img alt="" src="@/assets/illustrations/account.png" />
                      </div>
                    </div>
                  </div>

                  <!-- Sign in -->
                  <div v-if="!signedIn" class="pt-6 border-t">
                    {{ $i18n('already_have_an_account') }}
                    <a class="font-bold text-primary hover:underline cursor-pointer" @click="setAuthView('sign-in')">
                      {{ $i18n('sign_in') }} &raquo;
                    </a>
                  </div>
                </div>
              </div>
            </div>

            <!-- System -->
            <div v-if="section === 'system'" class="flex flex-col gap-4 items-start h-full Preferences__section">
              <div class="Form__group">
                <label class="Form__label">
                  {{ $i18n('options_options_show_in_context_menu') }}
                </label>
                <div>
                  <sl-switch
                    :checked="form.contextMenu"
                    @sl-change="updatePreferences('contextMenu', $event.target.checked)"
                  >
                    {{ $i18n('options_options_show_in_context_menu') }}
                  </sl-switch>
                </div>
              </div>

              <div class="w-full Form__group">
                <label class="Form__label">Keyboard Shortcut</label>
                <div class="flex justify-between items-center">
                  <div class="text-sm">Customize or remove the keyboard shortcut</div>
                  <div>
                    <sl-button size="small" @click="editShortcut()">Customize</sl-button>
                  </div>
                </div>
              </div>

              <div v-if="hasBackups" class="Form__group">
                <label class="Form__label">
                  {{ $i18n('pro_sync_backups') }}
                </label>
                <div v-for="(backup, index) in backups.backups" :key="index">
                  <a class="text-sm hover:text-primary" :href="backup.url" target="_blank">
                    {{ backup.bookmarks_count }} {{ $i18n('bookmarks') }}, {{ backup.groups_count }}
                    {{ $i18n('groups') }},
                    {{ relativeDate(backup.created_at) }}
                  </a>
                </div>
              </div>

              <div class="w-full Form__group">
                <label class="Form__label">
                  {{ $i18n('options_export_title') }}
                </label>
                <div class="flex space-x-4">
                  <div class="w-1/2">
                    <sl-button class="flex items-center space-x-2 w-full" @click="exportSettings">
                      <sl-icon class="text-lg" library="feather" name="save"></sl-icon>
                      <span>{{ $i18n('options_export_title') }}</span>
                    </sl-button>
                  </div>
                  <div class="w-1/2">
                    <sl-button
                      class="flex items-center space-x-2 w-full"
                      :loading="isImporting"
                      @click="importSettings"
                    >
                      <sl-icon class="text-lg" library="feather" name="upload"></sl-icon>
                      <span>{{ $i18n('options_import_title') }}</span>
                    </sl-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </sl-dialog>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep'
import _set from 'lodash/set'
import { loadScript } from 'vue-plugin-load-script'
import { mapActions, mapGetters } from 'vuex'

import FontPicker from '@/components/FontPicker'
import Gravatar from '@/components/Gravatar'
import ReloadBookmarksThumbnails from '@/components/ReloadBookmarksThumbnails'
import ResolveBookmarksWithoutThumbnail from '@/components/ResolveBookmarksWithoutThumbnail'
import ResolveDuplicateBookmarks from '@/components/ResolveDuplicateBookmarks'
import SidebarAnimation from '@/components/SidebarAnimation'
import defaultPreferences from '@/constants/defaultPreferences'
import themes from '@/constants/themes'
import DataService from '@/services/DataService'
import StorageService from '@/services/StorageService'
import SyncService from '@/services/SyncService'
import UploadService from '@/services/UploadService'
import { isChrome, isEdge, isFirefox } from '@/utils/browser'
import dayjs from '@/utils/dayjs'
import sleep from '@/utils/sleep'

const paddleScriptPath = 'vendor/paddle.js'

export default {
  components: {
    FontPicker,
    Gravatar,
    ResolveDuplicateBookmarks,
    ResolveBookmarksWithoutThumbnail,
    ReloadBookmarksThumbnails,
    SidebarAnimation
  },

  props: {
    preferences: {
      type: Object,
      required: true
    },

    hiddenGroups: {
      type: Array,
      required: true
    },

    bookmarksCount: {
      type: Number,
      default: 0
    },

    groups: {
      type: Array,
      required: true
    },

    isFreemium: {
      type: Boolean,
      required: false,
      default: false
    },

    isPremium: {
      type: Boolean,
      required: false,
      default: false
    },

    isLegacyPlan: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  emits: ['onChange', 'onChangeHiddenGroups', 'onCreateGroup'],

  data() {
    return {
      errors: {},
      profile: null,
      backups: null,
      username: '',
      password: '',
      themes,
      section: 'general',
      authView: null,
      syncConflictOption: 1,
      activeTheme: null,
      uploadResult: null,
      isDragOver: false,
      isUploading: false,
      isLoadingProfile: true,
      isSynchronizing: false,
      isProcessingPayment: false,
      isSubmittingAuthForm: false,
      isSyncConflict: false,
      isImporting: false,
      isSyncEnabled: true,
      swatches: ['#000', '#222', '#444', '#aaa', '#ccc', '#eee', '#fcfcfc', '#fff'],
      hasDiscount: false,
      currentTheme: null,
      isPreviewingTheme: false,
      thumbnailsPermission: false,
      isChrome: isChrome(),
      isEdge: isEdge(),
      isFirefox: isFirefox(),
      isDragging: false,
      offset: { x: 0, y: 0 }
    }
  },

  computed: {
    ...mapGetters('theme', ['effectiveTheme', 'recentUploads']),

    form: {
      get() {
        return this.preferences
      },
      set(value) {
        this.$emit('onChange', {
          preferences: cloneDeep(value),
          sync: this.isSyncEnabled
        })
      }
    },

    hiddenGroupsForm: {
      get() {
        return this.hiddenGroups
      },
      set(value) {
        this.$emit('onChangeHiddenGroups', value)
      }
    },

    coupon() {
      if (this.isLegacyPlan) {
        return 'DDF561DF'
      } else if (this.hasDiscount) {
        return 'D8990747'
      } else {
        return ''
      }
    },

    filteredThemes() {
      if (this.isPremium && !this.isLegacyPlan) {
        return this.themes
      } else {
        return this.themes.filter(({ premium }) => !premium)
      }
    },

    signedIn() {
      return [-1, 0, 1].includes(this.profile?.code)
    },

    premium() {
      return [0, 1].includes(this.profile?.code)
    },

    lastSync() {
      return this.profile?.last_sync ? dayjs.unix(this.profile.last_sync).fromNow() : '-'
    },

    subscriptionRenewsDate() {
      return this.profile?.subscription?.next_bill_date
        ? dayjs(this.profile.subscription.next_bill_date).format('MMMM D, YYYY')
        : '-'
    },

    subscriptionExpiresDate() {
      return this.profile?.subscription?.cancelation_effective_date
        ? dayjs(this.profile.subscription.cancelation_effective_date).format('MMMM D, YYYY')
        : '-'
    },

    hasBackups() {
      return this.backups?.code == 1
    }
  },

  watch: {
    section: {
      handler(value, oldValue) {
        if (value !== oldValue && value == 'account') {
          this.resetAccountSection()
        }
      }
    }
  },

  mounted() {
    this.activeTheme = this.preferences.theme.theme
    this.$refs.handle.addEventListener('mousedown', this.startDragging)
  },

  beforeDestroy() {
    // Clean up event listeners
    this.$refs.handle.removeEventListener('mousedown', this.startDragging)
  },

  methods: {
    ...mapActions('theme', ['storeRecentUpload', 'removeRecentUpload']),

    ...mapActions('bookmarks', ['init']),

    ...mapActions('preferences', ['reloadPreferences']),

    async initialize() {
      await this.init()
      await this.reloadPreferences()
    },

    resetDialog() {
      try {
        this.$refs.dialog.style.position = 'static'
        this.offset = { x: 0, y: 0 }
        this.isDragging = false
      } catch (e) {
        // Ignore
      }
    },

    async show(section = 'general') {
      this.resetDialog()
      this.$el.show()
      if (section == 'sign-in') {
        this.setSection('account')
        this.$nextTick(() => {
          this.setAuthView('sign-in')
        })
      } else {
        this.setSection(section)
        this.setAuthView(null)
      }
      this.isLoadingProfile = true
      await this.loadProfile()
      this.isLoadingProfile = false
      this.hasDiscount = StorageService.load('discount-07-2022', false)

      if (this.isFirefox) {
        browser.permissions.contains({ origins: ['<all_urls>'] }).then((granted) => {
          this.thumbnailsPermission = granted
        })
      }
    },

    setSection(section) {
      this.section = section
      if (!['theme', 'background'].includes(section)) {
        // Reset theme preview when leaving theme/background section
        if (this.form?.theme?.theme !== this.activeTheme) {
          this.updatePreferences('theme.theme', this.activeTheme)
        }
      }
      if (['background'].includes(section)) {
        this.currentTheme = { ...this.form.theme[this.effectiveTheme] }
      }
    },

    hide() {
      document.activeElement?.blur()
      this.$el.hide()
      this.updatePreferences('theme.theme', this.activeTheme)
    },

    resetTheme() {
      this.updatePreferences(`theme.${this.effectiveTheme}`, defaultPreferences.theme[this.effectiveTheme])
      this.updatePreferences('theme.font', defaultPreferences.theme.font)
      this.updatePreferences('theme.fontSize', defaultPreferences.theme.fontSize)
    },

    setBackground(url) {
      this.updatePreferences(`theme.${this.effectiveTheme}.backgroundImage`, url)
      this.setSection('theme')
    },

    setTheme() {
      this.isPreviewingTheme = false
      this.setSection('theme')
    },

    previewTheme(theme) {
      if (!this.isPremium && theme.premium) {
        return
      }
      this.isPreviewingTheme = true

      const previewTheme = {
        ...this.form.theme[this.effectiveTheme],
        backgroundImage: theme.url,
        backgroundColor: theme.color,
        backgroundOpacity: theme.opacity || '100',
        backgroundRepeat: theme.repeat,
        backgroundPosition: theme.position,
        backgroundSize: theme.size
      }

      this.updatePreferences(`theme.${this.effectiveTheme}`, previewTheme)
    },

    unPreviewTheme() {
      this.isPreviewingTheme = false
      this.updatePreferences(`theme.${this.effectiveTheme}`, { ...this.currentTheme })
      this.setSection('theme')
    },

    handleFileUpload(file) {
      if (!file) {
        return
      }

      this.errors.upload = null

      if (file.size >= 5000 * 1024) {
        this.errors.upload = 'Selected file too large. Maximum upload size is 5 MB.'
        return
      }

      this.isUploading = true

      UploadService.upload(file)
        .then((response) => {
          if (response?.url) {
            this.updatePreferences(`theme.${this.effectiveTheme}.backgroundImage`, response?.url)
            this.storeRecentUpload(response?.url)
          }
        })
        .catch((e) => {
          this.errors.upload = 'Error while uploading the file'
        })
        .finally(() => {
          this.isUploading = false
        })
    },

    handleDrop(e) {
      e.preventDefault()

      if (e.dataTransfer.items) {
        this.handleFileUpload(e.dataTransfer.items[0].getAsFile())
      } else {
        this.handleFileUpload(e.dataTransfer.files[0])
      }
    },

    handleDragLeave(e) {
      this.isDragOver = false
    },

    handleDragOver(e) {
      e.preventDefault()
      this.isDragOver = true
    },

    toggleGroupHiddenState(e, group) {
      let hiddenGroups
      if (e.target.checked) {
        hiddenGroups = this.hiddenGroupsForm.filter((id) => id !== group.id)
      } else {
        hiddenGroups = [...this.hiddenGroupsForm, ...[group.id]]
      }
      this.hiddenGroupsForm = [...new Set(hiddenGroups)]
    },

    toggleSidebar(enabled) {
      if (this.isFreemium) {
        return
      }

      if (enabled) {
        browser.permissions.request({ permissions: ['sessions'] }).then((granted) => {
          if (granted) {
            this.updatePreferences('sidebar', true)
          } else {
            this.updatePreferences('sidebar', false)
          }
        })
      } else {
        this.updatePreferences('sidebar', false)
      }
    },

    toggleBookmarks(enabled) {
      if (this.isFreemium) {
        return
      }

      if (enabled) {
        browser.permissions.request({ permissions: ['bookmarks'] }).then((granted) => {
          if (granted) {
            this.updatePreferences('sidebarBookmarks', true)
          } else {
            this.updatePreferences('sidebarBookmarks', false)
          }
        })
      } else {
        this.updatePreferences('sidebarBookmarks', false)
      }
    },

    uploadImage() {
      let uploader
      this.errors.upload = null

      uploader = document.createElement('input')
      uploader.type = 'file'
      uploader.accept = 'image/png, image/jpeg, image/gif'

      uploader.addEventListener('change', (e) => {
        this.handleFileUpload(e.target.files[0])
      })

      uploader.click()
    },

    relativeDate(date) {
      return dayjs(date).fromNow()
    },

    editShortcut() {
      let url

      if (this.isFirefox) {
        url = 'about:addons'
      } else if (this.isEdge) {
        url = 'edge://extensions/shortcuts'
      } else {
        url = 'chrome://extensions/shortcuts'
      }
      url && browser.tabs.create({ url })
    },

    async resolveSyncConflict() {
      this.isSyncConflict = false
      await DataService.backup()
      this.accountSync({ merge: Number(this.syncConflictOption) == 1 })
    },

    async handleAccountChange(poll = false) {
      await this.loadProfile()

      // Active account?
      if (this.profile.active) {
        this.isProcessingPayment = false
        this.isSyncConflict = false

        if (!this.profile.last_sync) {
          // Performs initial sync
          this.accountSync({ merge: true })
        } else if (this.bookmarksCount < 8) {
          this.accountSync({ merge: false })
        } else {
          // Ask users what to do...
          this.isSyncConflict = true
        }
      } else if (!this.profile.active && poll === true) {
        this.isProcessingPayment = true
        await sleep(2000)
        this.handleAccountChange(true)
      }
    },

    async syncNow() {
      await this.accountSync({ merge: false })
      await this.loadProfile()
    },

    async accountSync({ merge }) {
      this.isSynchronizing = true

      if (merge === true) {
        await DataService.merge()
      }

      await DataService.sync()

      setTimeout(async () => {
        await this.initialize()
        await this.loadProfile()
        this.isSynchronizing = false
        this.isSyncEnabled = true
      }, 1200)
    },

    async loadProfile() {
      this.profile = await SyncService.getProfile()
      this.backups = await SyncService.getBackups()
    },

    setAuthView(view) {
      this.errors.signIn = null
      this.errors.signUp = null
      this.password = ''
      this.authView = view
    },

    async signIn() {
      this.errors.signIn = null
      this.isSubmittingAuthForm = true
      const response = await SyncService.signIn({ username: this.username, password: btoa(this.password) })
      if (response.error) {
        this.errors.signIn = response.error
      } else if ([-1, 0, 1].includes(response?.code)) {
        this.isSyncEnabled = false
        this.updatePreferences('username', this.username)
        this.updatePreferences('password', btoa(this.password))
        await browser.storage.local.remove('hiddenGroups')
        this.$nextTick(() => {
          this.handleAccountChange()
        })
      } else {
        if (response?.code == -999) {
          this.errors.signIn = this.$i18n('sync_enter_credentials')
        }
        if (response?.code == -99) {
          this.errors.signIn = this.$i18n('sync_wrong_credentials')
        }
        this.updatePreferences('username', '')
        this.updatePreferences('password', '')
        this.password = ''
      }
      this.isSubmittingAuthForm = false
    },

    async signUp() {
      this.errors.signUp = null
      this.isSubmittingAuthForm = true
      const response = await SyncService.signUp({ username: this.username, password: this.password })
      if (response.error) {
        this.errors.signUp = response.error
      } else if (response.email) {
        this.updatePreferences('username', response.email)
        this.updatePreferences('password', btoa(this.password))
        this.$nextTick(() => {
          this.loadProfile()
        })
      }
      this.isSubmittingAuthForm = false
    },

    signOut() {
      this.updatePreferences('username', '')
      this.updatePreferences('password', '')
      this.username = ''
      this.password = ''
      this.profile = null
      this.backups = null
      this.setAuthView(null)
      this.resetAccountSection()
    },

    resetAccountSection() {
      this.setAuthView(null)
      this.password = ''
      this.isSyncConflict = false
      this.syncConflictOption = 1
      this.isSubmittingAuthForm = false
    },

    async initializePaddle() {
      if (this.isFirefox) {
        return
      }

      await loadScript(paddleScriptPath)

      Paddle.Setup({
        vendor: 110419
      })
    },

    async checkout() {
      if (this.isFirefox) {
        window.open(
          `https://www.speeddial2.com/upgrade?email=${encodeURIComponent(this.profile.email)}&coupon=${
            this.coupon || ''
          }`,
          '_blank'
        )

        return
      }

      await this.initializePaddle()

      const handleCheckout = (result) => {
        if (result.checkout && result.checkout.completed === true) {
          this.$nextTick(() => {
            this.handleAccountChange(true)
          })
        }
      }

      Paddle.Checkout.open({
        email: this.profile.email,
        passthrough: this.profile.user,
        coupon: this.coupon,
        product: 781575,
        allowQuantity: false,
        disableLogout: true,
        successCallback: handleCheckout
      })
    },

    async updateSubscription() {
      if (this.isFirefox) {
        window.open(this.profile?.subscription?.update_url, '_blank')
        return
      }

      await this.initializePaddle()

      Paddle.Checkout.open({
        override: this.profile?.subscription?.update_url
      })
    },

    async cancelSubscription() {
      window.open(this.profile?.subscription?.cancel_url, '_blank')

      // if (this.isFirefox) {
      //   window.open(this.profile?.subscription?.cancel_url, '_blank')
      //   return
      // }

      // await this.initializePaddle()

      // const handleCancellation = () => {
      //   this.$nextTick(() => {
      //     this.syncNow()
      //   })
      // }

      // Paddle.Checkout.open({
      //   override: this.profile?.subscription?.cancel_url,
      //   successCallback: handleCancellation
      // })
    },

    async importSettings() {
      let uploader
      uploader = document.createElement('input')
      uploader.type = 'file'
      uploader.accept = 'application/JSON'

      uploader.addEventListener('change', (e) => {
        const reader = new FileReader()
        const file = e.target.files[0]

        if (!file) {
          return
        }

        reader.onload = (input) => {
          const warningMessage = [
            this.$i18n('options_import_warning_text_1'),
            this.$i18n('options_import_warning_text_2')
          ].join(' ')
          if (confirm(warningMessage)) {
            this.isImporting = true
            DataService.import(input.target.result).then(() => {
              this.isImporting = false
              this.initialize()
            })
          }
        }
        reader.readAsText(file)
      })

      uploader.click()
    },

    async exportSettings() {
      const data = await DataService.export()
      const date = new Date().toISOString().slice(0, 10)
      const link = document.createElement('a')
      link.setAttribute('href', 'data:Application/octet-stream,' + encodeURIComponent(JSON.stringify(data)))
      link.setAttribute('download', `speed-dial-2-export-${date}.json`)
      document.body.appendChild(link)
      link.click()
      link.remove()
    },

    createGroup() {
      this.$emit('onCreateGroup')
      this.hide()
    },

    decreaseFontSize(val) {
      this.updatePreferences('theme.fontSize', Math.max(80, this.form.theme.fontSize - 10))
    },

    increaseFontSize(val) {
      this.updatePreferences('theme.fontSize', Math.min(140, this.form.theme.fontSize + 10))
    },

    setFont(val) {
      this.updatePreferences('theme.font', val)
      this.setSection('theme')
    },

    onRequestClose(e) {
      e.preventDefault()
    },

    updatePreferences(path, value) {
      _set(this.form, path, value)

      this.form = cloneDeep(this.form)

      if (path === 'contextMenu') {
        browser.runtime.sendMessage({ request: 'update-context-menu' })
      }
    },

    requestPermissions() {
      browser.permissions.request({ origins: ['<all_urls>'] }).then((granted) => {
        this.thumbnailsPermission = granted
      })
    },

    startDragging(e) {
      this.isDragging = true
      this.offset.x = e.clientX - this.$refs.dialog.getBoundingClientRect().left
      this.offset.y = e.clientY - this.$refs.dialog.getBoundingClientRect().top

      document.addEventListener('mousemove', this.onDragging)
      document.addEventListener('mouseup', this.stopDragging)
    },
    onDragging(e) {
      if (this.isDragging) {
        this.$refs.dialog.style.width = this.$refs.dialog.offsetWidth + 'px'
        this.$refs.dialog.style.left = `${e.clientX - this.offset.x}px`
        this.$refs.dialog.style.top = `${e.clientY - this.offset.y}px`
        this.$refs.dialog.style.position = 'absolute' // Ensure the position is absolute
      }
    },
    stopDragging() {
      this.isDragging = false
      document.removeEventListener('mousemove', this.onDragging)
      document.removeEventListener('mouseup', this.stopDragging)
    }
  }
}
</script>

