<template>
  <div v-if="bookmarksWithNoThumbnail.length" class="pt-8 border-t">
    <div class="Form__group">
      <label class="Form__label">Missing thumbnails</label>
      <div class="text-sm">
        <div v-if="isRegeneratingThumbnails">
          Processing {{ isRegeneratingProcessedItems }} of {{ bookmarksWithNoThumbnail.length }}
        </div>
        <div v-else>
          {{ bookmarksWithNoThumbnail.length }} bookmarks have a missing thumbnail.
          <a class="text-primary cursor-pointer" @click="regenerateThumbnails()">Regenerate thumbnails.</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import createPageScreenshot from '@/utils/createPageScreenshot'

export default {
  data() {
    return {
      isRegeneratingThumbnails: false,
      isRegeneratingProcessedItems: 0
    }
  },

  computed: {
    ...mapGetters('bookmarks', ['bookmarksWithNoThumbnail'])
  },

  mounted() {
    this.getBookmarksWithNoThumbnail()
  },

  methods: {
    ...mapActions('bookmarks', ['editBookmark', 'getBookmarksWithNoThumbnail']),

    async regenerateThumbnails() {
      this.isRegeneratingThumbnails = true
      this.isRegeneratingProcessedItems = 1

      // order this.bookmarksWithNoThumbnail by random
      const randomizedBookmarks = [...this.bookmarksWithNoThumbnail].sort(() => Math.random() - 0.5)

      for (const bookmark of randomizedBookmarks) {
        bookmark.dataImage = await createPageScreenshot(bookmark.url)
        bookmark.thumbnail = ''
        await this.editBookmark(bookmark)
        this.isRegeneratingProcessedItems += 1
      }

      await this.getBookmarksWithNoThumbnail()
      this.isRegeneratingThumbnails = false
    }
  }
}
</script>
