<template>
  <div class="overflow-hidden relative ResponsiveAspectRatio">
    <div :style="innerStyle">
      <div class="absolute top-0 left-0 w-full h-full">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    ratio: {
      type: Number,
      default: 1
    }
  },

  computed: {
    innerStyle() {
      return { paddingTop: 100 * this.ratio + '%' }
    }
  }
}
</script>
