<template>
  <div class="SidebarWrapper" :style="sidebarStyle" @mouseenter="show">
    <sl-drawer
      ref="sidebar"
      no-header
      :style="`--size: ${width}; --sl-overlay-background-color: rgba(0,0,0,0.05); --body-spacing: 0`"
      @sl-after-hide="afterHide"
      @sl-after-show="afterShow"
      @sl-request-close="requestClose"
    >
      <div v-if="isOpen" class="Sidebar">
        <div v-if="preferences.sidebarBookmarks" class="Sidebar__panel">
          <BrowserBookmarks
            ref="browserBookmarks"
            :preferences="preferences"
            @onUpdatePreferences="$emit('onUpdatePreferences', $event)"
          />
        </div>
        <div v-if="preferences.sidebar" class="Sidebar__panel">
          <RecentlyClosedTabs />
        </div>
      </div>
    </sl-drawer>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

import BrowserBookmarks from '@/components/BrowserBookmarks.vue'
import RecentlyClosedTabs from '@/components/RecentlyClosedTabs.vue'

export default {
  components: {
    RecentlyClosedTabs,
    BrowserBookmarks
  },

  props: {
    preferences: {
      type: Object,
      required: true
    }
  },

  emits: ['onUpdatePreferences'],

  data() {
    return {
      isOpen: false
    }
  },

  computed: {
    ...mapGetters('theme', ['sidebarStyle']),

    width() {
      return this.preferences.sidebar && this.preferences.sidebarBookmarks ? '40rem' : '20rem'
    }
  },

  methods: {
    hide() {
      this.$refs.sidebar.hide()
    },

    show() {
      if (this.isOpen) {
        return
      }

      this.$refs.sidebar.show()
    },

    onLeave() {
      this.hide()
    },

    afterHide(e) {
      if (e.target?.tagName == 'SL-SELECT' || e.target?.tagName == 'SL-DROPDOWN') {
        this.isOpen = true
      } else {
        this.isOpen = false
      }
    },

    afterShow() {
      this.$refs.sidebar.shadowRoot?.querySelector('.drawer__panel')?.addEventListener('mouseleave', this.onLeave)
      this.isOpen = true
    },

    requestClose(e) {
      if (this.preferences.sidebarBookmarks) {
        e.preventDefault()
        this.$refs.browserBookmarks?.focusSearch()
      }
    },

    beforeUnmount() {
      this.$refs.sidebar.shadowRoot?.querySelector('.drawer__panel')?.removeEventListener('mouseleave', this.onLeave)
    }
  }
}
</script>

<style lang="postcss">
.SidebarWrapper {
  position: fixed;
  z-index: 100;
  width: 1rem;
  right: 0;
  top: 4rem;
  bottom: 0;
}
.Sidebar {
  @apply h-screen divide-x;
  display: grid;
  grid-auto-columns: minmax(0, 1fr);
  grid-auto-flow: column;

  &__panel {
    @apply h-full overflow-hidden;
  }
}

.SidebarPanel {
  @apply flex flex-col h-full;

  &__header {
    @apply p-4 py-4 border-b flex-shrink-0 font-medium;

    &--no-border {
      @apply border-0;
    }
  }
  &__nav {
    @apply p-4 pt-0 -mt-1 border-b flex-shrink-0;
  }
  &__body {
    @apply p-3 flex-1 overflow-y-auto;
  }
  &__footer {
    @apply p-3 border-t flex-shrink-0;
  }
}
</style>
