<template>
  <a
    class="Bookmark"
    :class="{ 'Bookmark--selected': bookmark.selected }"
    :data-id="bookmark.id"
    :href="href"
    :style="bookmarkWrapperStyle"
    @click="handleClick"
    @contextmenu.prevent="handleContextMenu"
    @dragstart="handleDragStart"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave"
  >
    <ResponsiveAspectRatio class="w-full" :ratio="aspectRatio" :style="bookmarkStyle">
      <div v-if="isReloading" class="flex justify-center items-center h-full">
        <sl-spinner class="mx-auto" size="small" />
      </div>
      <div v-else-if="isLoading"></div>
      <div v-else-if="clearbitLogo" class="h-full" :class="{ 'p-4': preferences.columns < 6 }">
        <img
          ref="logo"
          class="object-contain mx-auto w-auto h-full rounded-lg"
          draggable="false"
          loading="lazy"
          :src="logo"
        />
      </div>
      <img
        v-else
        ref="logo"
        class="w-full h-full"
        :class="{ 'object-contain': shouldContainLogo, 'object-cover object-top': !shouldContainLogo }"
        draggable="false"
        loading="lazy"
        :src="logo"
      />
    </ResponsiveAspectRatio>
    <div v-if="disabled || preferences.bookmarks.showTitle" class="Bookmark__title" :style="bookmarkTitleStyle">
      {{ bookmark.title }}
      <span v-if="preferences.bookmarks.showVisits && bookmark.visits" class="inline-block ml-1 text-xs opacity-50">
        {{ bookmark.visits }}
      </span>
    </div>
    <div v-if="isSearchResult" class="-mt-1 mb-2 text-xs text-muted">
      {{ createdAt }}
      <span
        v-if="bookmark.groupTitle"
        class="text-primary hover:underline"
        @click.stop="openGroup($event, bookmark.idgroup)"
      >
        in
        {{ bookmark.groupTitle }}
      </span>
    </div>

    <div
      v-show="disabled || isMouseOver"
      class="left-0 w-5 h-5 text-white rounded Bookmark__action"
      :class="{
        'bg-primary border-primary text-white': bookmark.selected,
        'bg-white border border-gray-300 hover:border-gray-400': !bookmark.selected
      }"
      @click="selectBookmark"
    >
      <sl-icon class="w-3" library="feather" name="check"></sl-icon>
    </div>

    <div
      v-show="isMouseOver"
      class="right-0 w-6 h-6 hover:text-white bg-white hover:bg-primary rounded-full Bookmark__action"
      @click="handleEdit"
    >
      <sl-icon class="w-4" library="feather" name="more-horizontal"></sl-icon>
    </div>
  </a>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import noThumbnail from '@/assets/no-thumbnail.png'
import ResponsiveAspectRatio from '@/components/ResponsiveAspectRatio'
import StorageService from '@/services/StorageService'
import { isFirefox } from '@/utils/browser'

const logoMigration = {
  'https://i.imgur.com/jf2eFbd.png': 'https://assets.speeddial2.com/logo/netflix.png',
  'https://i.imgur.com/hX1weOx.png': 'https://assets.speeddial2.com/logo/google.png',
  'https://i.imgur.com/ckpKdOX.png': 'https://assets.speeddial2.com/logo/gmail.png',
  'https://i.imgur.com/jXqoRgO.png': 'https://assets.speeddial2.com/logo/aws.png',
  'https://i.imgur.com/oqVZgBY.png': 'https://assets.speeddial2.com/logo/bing.png',
  'https://i.imgur.com/2VSeKcw.png': 'https://assets.speeddial2.com/logo/facebook.png',
  'https://i.imgur.com/yEC5MsS.png': 'https://assets.speeddial2.com/logo/youtube.png',
  'https://i.imgur.com/CaIoUbv.png': 'https://assets.speeddial2.com/logo/twitter.png',
  'https://i.imgur.com/I5A9HUI.png': 'https://assets.speeddial2.com/logo/google-calendar.png',
  'https://i.imgur.com/8nXj9yL.png': 'https://assets.speeddial2.com/logo/google-maps.png',
  'https://i.imgur.com/fOnIbPu.png': 'https://assets.speeddial2.com/logo/google-drive.png',
  'https://i.imgur.com/ktYBEbY.png': 'https://assets.speeddial2.com/logo/google-photos.png',
  'https://i.imgur.com/csdt6kb.png': 'https://assets.speeddial2.com/logo/google-docs.png',
  'https://i.imgur.com/5Gub2kR.png': 'https://assets.speeddial2.com/logo/google-sheets.png',
  'https://i.imgur.com/UkVYfpn.png': 'https://assets.speeddial2.com/logo/google-slides.png',
  'https://i.imgur.com/00td40b.png': 'https://assets.speeddial2.com/logo/google-hangouts.png',
  'https://i.imgur.com/gTHRNjR.png': 'https://assets.speeddial2.com/logo/google-meet.png',
  'https://i.imgur.com/XQnFvQr.png': 'https://assets.speeddial2.com/logo/microsoft-office.png',
  'https://i.imgur.com/r8CrR51.png': 'https://assets.speeddial2.com/logo/microsoft-outlook.png',
  'http://i.imgur.com/NOs1h12.png': 'https://assets.speeddial2.com/logo/dropbox-paper.png',
  'http://i.imgur.com/ujADvWC.png': 'https://assets.speeddial2.com/logo/taobao.png',
  'http://i.imgur.com/Mj0u4Ts.png': 'https://assets.speeddial2.com/logo/youtube.png',
  'http://i.imgur.com/3WdnTuH.png': 'https://assets.speeddial2.com/logo/google.png',
  'http://i.imgur.com/5azi974.png': 'https://assets.speeddial2.com/logo/facebook.png',
  'http://i.imgur.com/9KcsUAc.png': 'https://assets.speeddial2.com/logo/amazon.png',
  'http://i.imgur.com/liQSBMB.png': 'https://assets.speeddial2.com/logo/google-docs.png',
  'http://i.imgur.com/66Zr5MP.png': 'https://assets.speeddial2.com/logo/youtube.png',
  'http://i.imgur.com/dgP5xHs.png': 'https://logo.clearbit.com/www.ebay.com?size=320'
}

export default {
  components: {
    ResponsiveAspectRatio
  },

  props: {
    bookmark: {
      type: Object
    },
    preferences: {
      type: Object,
      default: () => {},
      required: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    isPremium: {
      type: Boolean,
      default: false
    },
    isSearchResult: {
      type: Boolean,
      default: false
    },
    isReloading: {
      type: Boolean,
      default: false
    }
  },

  emits: ['onContextMenu', 'onSelected', 'onVisit', 'onOpenGroup'],

  data() {
    return {
      isLoading: false,
      isMouseOver: false,
      mouseOverTimeout: null,
      lazyLoadedThumbnail: null
    }
  },

  computed: {
    ...mapGetters('theme', ['bookmarkStyle', 'bookmarkWrapperStyle', 'bookmarkTitleStyle']),
    aspectRatio() {
      const modifier = this.preferences.bookmarks.showTitle ? 0.9 : 1
      if (screen.height > screen.width && screen.height / screen.width >= 1.5) {
        return Math.max((screen.width * modifier) / screen.height, 9 / 16)
      } else {
        return Math.max((screen.height * modifier) / screen.width, 9 / 16)
      }
    },
    clearbitLogo() {
      return this.bookmark.thumbnail.indexOf('https://logo.clearbit.com') === 0
    },
    title() {
      return this.bookmark.title || this.hostname
    },
    href() {
      return this.bookmark.url
        ? this.bookmark.url
        : this.$router.resolve({ name: 'folder', params: { id: this.bookmark.id } }).href
    },
    hostname() {
      const hostname = (this.bookmark.url || '').split(/\/+/g)[1]
      return (hostname || '').replace('www.', '')
    },
    parent() {
      return this.bookmark.parents[this.bookmark.parents.length - 1]
    },
    isLogo() {
      return this.bookmark.thumbnail
    },
    hasLogo() {
      return this.bookmark.thumbnail || this.bookmark.dataThumbnail || this.lazyLoadedThumbnail
    },
    shouldContainLogo() {
      return this.bookmark.thumbnail || (!this.hasLogo && !this.useRasterizerImage)
    },
    defaultImage() {
      return this.useRasterizerImage ? this.rasterizerImage : noThumbnail
    },
    useRasterizerImage() {
      return StorageService.load('isPremium', false) && this.bookmark.ts_created > 1398902400
    },
    rasterizerImage() {
      return `https://www.speeddial2.com/screenshot/?url=${encodeURI(this.bookmark.url)}`
    },
    createdAt() {
      if (!this.bookmark?.ts_created) {
        return
      }

      return new Date(this.bookmark.ts_created * 1000).toLocaleString([], {
        day: 'numeric',
        month: 'short',
        year: 'numeric'
      })
    },
    logo() {
      /*
        Other possible alternatives:
        `https://iconbin.com/api/${this.bookmahostname}/src`
        `https://api.faviconkit.com/${this.hostname}`
        `https://favicone.com/google.com?s=32`
        `chrome://favicon/size/48@1x${this.bookmark.url}`
        `chrome://favicon2/?size=32&scale_factor=2x&page_url=https%3A%2F%2Fmail.google.com%2Fmail%2Fu%2F0%2F%23inbox`
      */
      // return `https://iconbin.com/api/${this.bookmahostname}/src`
      // return `chrome://favicon2/?size=32&scale_factor=2x&page_url=https%3A%2F%2Fmail.google.com%2Fmail%2Fu%2F0%2F%23inbox`
      // return this.bookmark.thumbnail || `https://logo.clearbit.com/${this.hostname}?size=320`
      // return `https://rdl.ink/render/${encodeURIComponent(this.bookmark.url)}`
      // return `https://logo.clearbit.com/${this.hostname}`
      // return `https://logo.clearbit.com/${this.hostname}`
      // return `https://icon.horse/icon/${this.hostname}`

      let thumbnail = this.bookmark.thumbnail

      if (thumbnail && logoMigration[thumbnail]) {
        thumbnail = logoMigration[thumbnail]
      }

      return thumbnail || this.bookmark.dataThumbnail || this.lazyLoadedThumbnail || this.defaultImage
    }
  },
  async created() {
    // Firefox: Lazy load dataThumbnail
    if (!this.bookmark.thumbnail && typeof this.bookmark.dataThumbnail === 'undefined' && isFirefox()) {
      this.isLoading = true
      this.lazyLoadedThumbnail = await this.getThumbnail(this.bookmark)
      this.isLoading = false
    }
  },
  mounted() {
    this.registerLogoFallback()
  },
  unmounted() {
    this.unregisterLogoFallback()
  },
  methods: {
    ...mapActions('bookmarks', ['getThumbnail']),

    handleContextMenu($event) {
      this.$emit('onContextMenu', $event, { bookmark: this.bookmark })
    },
    selectBookmark(e) {
      this.$emit('onSelected', e, { bookmark: this.bookmark })
      e.preventDefault()
      e.stopPropagation()
    },
    handleClick(e) {
      if (this.disabled) {
        return this.selectBookmark(e)
      }

      if (this.preferences.openInNewTab) {
        window.open(this.bookmark.url)
        e.preventDefault()
      } else if (!e.metaKey && !e.ctrlKey) {
        browser.tabs.query({ active: true }).then((tab) => {
          browser.tabs.update(tab.id, { url: this.bookmark.url })
        })
      }

      this.$emit('onVisit', e, this.bookmark.id)
    },
    handleEdit(e) {
      this.$emit('onEdit', e, this.bookmark)
      e.preventDefault()
      e.stopPropagation()
    },
    handleMouseEnter() {
      this.mouseOverTimeout = setTimeout(() => {
        this.isMouseOver = true
      }, 2000)
    },
    handleMouseLeave() {
      clearTimeout(this.mouseOverTimeout)
      this.isMouseOver = false
    },
    useFallbackLogo() {
      this.unregisterLogoFallback()
      // this.$refs.logo.setAttribute('src', `https://favicone.com/${this.hostname}?s=32`)
      // this.$refs.logo.setAttribute('src', `https://api.faviconkit.com/${this.hostname}/320`)
      this.$refs.logo.setAttribute('src', `https://cdn.brandfetch.io/${this.hostname}/fallback/transparent`)
    },
    registerLogoFallback() {
      this.$refs.logo && this.$refs.logo.addEventListener('error', this.useFallbackLogo)
    },
    unregisterLogoFallback() {
      this.$refs.logo && this.$refs.logo.removeEventListener('error', this.useFallbackLogo)
    },
    handleDragStart(event) {
      const data = { type: 'bookmark', id: this.bookmark.id }
      event.dataTransfer.setData('application/json', JSON.stringify(data))
    },
    openGroup(event, id) {
      event.preventDefault()
      event.stopPropagation()
      this.$emit('onOpenGroup', event, id)
    }
  }
}
</script>

<style lang="postcss" scoped>
.Bookmark {
  @apply border overflow-hidden relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  text-decoration: none;
  background-clip: padding-box;
  font-size: 14px;

  &--selected {
    box-shadow: 0 0 0 1px theme('colors.primary');
  }

  &__title {
    @apply w-full truncate;
    margin: 0 auto;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &__action {
    @apply flex absolute top-0 flex-shrink-0 justify-center items-center m-2.5;
    animation-duration: 0.25s;
    animation-name: animate-fade;
    animation-fill-mode: backwards;
  }
}

@keyframes animate-fade {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
</style>
