import { getParent, getParents, sortBookmarks } from '@/utils/bookmarks'
import { isFirefox } from '@/utils/browser'

const types = {
  SET_APP_LOADING: 'SET_APP_LOADING',
  SET_BOOKMARKS: 'SET_BOOKMARKS',
  CURRENT_FOLDER_SET: 'CURRENT_FOLDER_SET',
  SEARCH_QUERY_SET: 'SEARCH_QUERY_SET',
  SEARCH_RESULTS_SET: 'SEARCH_RESULTS_SET'
}

const getInitialState = () => ({
  isLoading: false,
  bookmarks: [],
  searchResults: [],
  currentFolder: undefined,
  query: undefined
})

const rootFolderId = isFirefox() ? 'toolbar_____' : '1'

const state = getInitialState()

const actions = {
  async init({ commit, dispatch, state, rootState }) {
    commit(types.SET_APP_LOADING, true)
    browser.bookmarks.getTree().then((result) => {
      if (result.length) {
        const manualOrder = rootState.preferences.preferences.sidebarBookmarksOrder == 'manual'
        let bookmarks, folders, expandedFolders, bookmarksBar

        if (manualOrder) {
          bookmarks = result[0].children.find(({ id }) => id == rootFolderId).children
          folders = result[0].children.filter(({ id }) => id != rootFolderId)
          commit(types.SET_BOOKMARKS, [...bookmarks, ...folders])
        } else {
          bookmarksBar = result[0].children.find(({ id }) => id == rootFolderId) || null
          folders = result[0].children.filter(({ id }) => id != rootFolderId) || []
          expandedFolders = sortBookmarks(folders.flatMap((folder) => folder.children))

          commit(types.SET_BOOKMARKS, [bookmarksBar, ...expandedFolders].filter(Boolean))
        }
      }

      commit(types.SET_APP_LOADING, false)
    })
  },

  async setFolder({ commit, rootState }, id) {
    const manualOrder = rootState.preferences.preferences.sidebarBookmarksOrder == 'manual'
    commit(types.SET_APP_LOADING, true)
    commit(types.SEARCH_QUERY_SET, undefined)
    commit(types.SEARCH_RESULTS_SET, [])

    try {
      const tree = await browser.bookmarks.getSubTree(id)
      const folder = {
        ...tree[0],
        parents: await getParent(tree[0]),
        bookmarks: manualOrder ? tree[0].children : sortBookmarks(tree[0].children)
      }

      commit(types.CURRENT_FOLDER_SET, folder)
    } catch (e) {
      commit(types.CURRENT_FOLDER_SET, undefined)
    }

    commit(types.SET_APP_LOADING, false)
  },

  async searchBookmarks({ commit, dispatch }, query) {
    commit(types.SET_APP_LOADING, true)
    commit(types.CURRENT_FOLDER_SET, undefined)

    if (query) {
      const limit = query.length > 5 ? 999 : 12
      const result = await browser.bookmarks.search(query)
      let bookmarks = sortBookmarks(result).slice(0, limit)
      bookmarks = await getParents(bookmarks)

      commit(types.SEARCH_RESULTS_SET, bookmarks)
    } else {
      commit(types.SEARCH_RESULTS_SET, [])
    }

    commit(types.SEARCH_QUERY_SET, query)
    commit(types.SET_APP_LOADING, false)
  }
}

const mutations = {
  [types.SET_BOOKMARKS](state, bookmarks) {
    state.bookmarks = bookmarks
  },

  [types.SEARCH_QUERY_SET](state, obj) {
    state.query = obj
  },

  [types.SEARCH_RESULTS_SET](state, obj) {
    state.searchResults = obj
  },

  [types.CURRENT_FOLDER_SET](state, obj) {
    state.currentFolder = obj
  },

  [types.SET_APP_LOADING](state, isLoading) {
    state.isLoading = isLoading
  }
}

const getters = {
  bookmarks(state) {
    return state.bookmarks
  },

  query(state) {
    return state.query
  },

  searchResults(state) {
    return state.searchResults
  },

  folder(state) {
    return state.currentFolder
  },

  isLoading(state) {
    return state.isLoading
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
