<template>
  <div v-if="isVisible" class="pt-8 border-t">
    <div class="Form__group">
      <div class="flex justify-between items-center w-full">
        <div class="w-full">
          <label class="Form__label">Reload all thumbnails</label>
          <div class="text-sm">
            <div v-if="isRegeneratingThumbnails">
              Processing {{ isRegeneratingProcessedItems }} of {{ bookmarksWithThumbnail.length }}
            </div>
            <div v-else>
              <a
                class="text-primary cursor-pointer"
                :class="{ 'cursor-not-allowed opacity-50': !isPremium }"
                @click="regenerateThumbnails()"
              >
                Reload {{ bookmarksWithThumbnail.length }} thumbnails.
              </a>
            </div>
          </div>
        </div>
        <div>
          <div
            v-if="!isPremium"
            class="p-1 px-2 mr-2 text-xs font-bold text-white bg-red-400 hover:bg-red-500 rounded cursor-pointer"
            @click="$emit('upgrade')"
          >
            PRO
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

import createPageScreenshot from '@/utils/createPageScreenshot'

export default {
  props: {
    isPremium: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  data() {
    return {
      isRegeneratingThumbnails: false,
      isRegeneratingProcessedItems: 0
    }
  },

  computed: {
    ...mapGetters('bookmarks', ['bookmarksWithThumbnail']),

    isVisible() {
      return this.bookmarksWithThumbnail?.length && this.bookmarksWithThumbnail.length > 16
    }
  },

  mounted() {
    this.getBookmarksWithThumbnail()
  },

  methods: {
    ...mapActions('bookmarks', ['editBookmark', 'getBookmarksWithThumbnail']),

    async regenerateThumbnails() {
      if (!this.isPremium) {
        return
      }

      this.isRegeneratingThumbnails = true
      this.isRegeneratingProcessedItems = 1

      for (const bookmark of this.bookmarksWithThumbnail) {
        bookmark.dataImage = await createPageScreenshot(bookmark.url)
        bookmark.thumbnail = ''
        await this.editBookmark(bookmark)
        this.isRegeneratingProcessedItems += 1
      }

      this.isRegeneratingThumbnails = false
    }
  }
}
</script>
